define([
    './allowance-charge.directive',
    './allowance-charge.controller',
    './reason-selector/_loader'
], function (
    diAllowanceChargeDirectiveLoader,
    allowanceChargeControllerLoader,
    reasonSelectorLoader) {
    'use strict';

    return function (module) {
        diAllowanceChargeDirectiveLoader(module);
        allowanceChargeControllerLoader(module);
        reasonSelectorLoader(module);
    };
});


