// di.einvoicing.proxies.formatters
define([
    './invoice-formatter.factory',
    './purchase-order-formatter.factory',
    './grn-formatter.factory',
    './date-formatter.factory'
], function(
    invoiceFormatterLoader,
    purchaseOrderFormatterLoader,
	grnFormatterLoader,
	dateFormatterLoader) {
    'use strict';

    return function(module) {
        invoiceFormatterLoader(module);
        purchaseOrderFormatterLoader(module);
		grnFormatterLoader(module);
		dateFormatterLoader(module);
    };
});


