define([
	'./_states',
    './account.service',
	'./settings/_loader',
    './account-management/_loader',
    './notifications-settings/_loader',
    './security-question/_loader'
], function (
	statesLoader,
    accountService,
	settingsLoader,
	accountManagementLoader,
    notificationSettingsLoader,
    securityQuestionLoader) {
	'use strict';

	return function (module) {
	    statesLoader(module);

	    module.service('accountService', accountService.AccountService);

		settingsLoader(module);
		accountManagementLoader(module);
		notificationSettingsLoader(module);
	    securityQuestionLoader(module);
	};
});



