define([
    './document-state-resolver.factory',
    './loader'
], function (documentStateResolverLoader,
    documentStateSelectorComponentLoader) {
    'use strict';

    return function (module) {
        documentStateResolverLoader(module);
        documentStateSelectorComponentLoader.load(module);
    };
});




