//{CODE:CAU0H}
// di.core.angular.utility
define(function () {
	'use strict';

	return function (module) {
		module.directive('diTranscludeReplace', diTranscludeReplace);

		function diTranscludeReplace() {
		    return {
                terminal: true,
				restrict: 'EA',
				link: link
			};

		    function link(scope, element, attr, ctrl, transclude) {
		        if (!transclude) {
		            $di.exception.guard.throwNew(
                        "CAU0H01E",
                        'Illegal use of diTranscludeReplace directive in the template! ' +
                               'No parent directive that requires a transclusion found. ');
		        }

		        transclude(function (clone) {
		            if (clone.length) {
		                element.replaceWith(clone);
		            }
		            else {
		                element.remove();
		            }
		        });
		    }
		}
	};
});


