define([
    './_components/_loader',
    './contract-type-code-selector/_loader',
    './currency-selector/_loader',
    './location-selector/_loader',
    './invoiceController',
    './invoiceTotalsCalculator',
    './invoiceDetailsButtonsConfigurator',
    './contact-details/_loader',
    './contact-selector/_loader',
    './companies-selector/_loader'
], function (
    componentsLoader,
    contractTypeCodeSelectorLoader,
    currencySelectorLoader,
    locationSelectorLoader,
    invoiceControllerLoader,
    invoiceTotalsCalculatorLoader,
    invoiceDetailsButtonsConfiguratorLoader,
    contactDetailsLoader,
    contactSelectorLoader,
    companiesSelectorLoader) {
    'use strict';

    return function (module) {
        componentsLoader(module);
        contractTypeCodeSelectorLoader(module);
        currencySelectorLoader(module);
        locationSelectorLoader(module);
        invoiceControllerLoader(module);
        invoiceTotalsCalculatorLoader(module);
        invoiceDetailsButtonsConfiguratorLoader(module);
        contactDetailsLoader(module);
        contactSelectorLoader(module);
        companiesSelectorLoader(module);
    };
});



