// di.core.angular.authentication
define([
	'./_config',
	'./authenticationClientEventSubscriber',
	'./authenticationController',
	'./diAuthenticationDirective',
	'./authenticationHttpInterceptor',
	'./authenticationData',
	'./authenticationStore',
	'./userAuthenticationProxy',
	'./authenticationTicketValidator',
	'./authorizedObjectsFilter',
	'./userRightsRepository',
	'./authenticationPromptConfigurator',
	'./authenticationResult',
	'./loggedOutReason',
    './userIdleActivityMonitor',
    './activityMonitor'
], function (
	configLoader,
	authenticationClientEventSubscriberLoader,
	authenticationControllerLoader,
	authenticationDirectiveLoader,
	authenticationHttpInterceptorLoader,
	authenticationDataLoader,
	authenticationStoreLoader,
	userAuthenticationProxyLoader,
	authenticationTicketValidatorLoader,
	authorizedObjectsFilterLoader,
	userRightsRepositoryLoader,
	authenticationPromptConfiguratorLoader,
	authenticationResultLoader,
	loggedOutReasonLoader,
    userIdleActivityMonitorLoader,
    activityMonitor
	) {
	'use strict';

	return function (module) {
	    userIdleActivityMonitorLoader(module);
		authenticationClientEventSubscriberLoader(module);
		authenticationControllerLoader(module);
		authenticationDirectiveLoader(module);
		authenticationHttpInterceptorLoader(module);
		authenticationDataLoader(module);
		authenticationStoreLoader(module);
		userAuthenticationProxyLoader(module);
		authenticationTicketValidatorLoader(module);
		authorizedObjectsFilterLoader(module);
		userRightsRepositoryLoader(module);
		authenticationPromptConfiguratorLoader(module);
		configLoader(module);
		authenticationResultLoader(module);
		loggedOutReasonLoader(module);
	    activityMonitor(module);
	};
});



