define([
    './product-selector.directive',
    './product-selector.controller'
], function (
    diProductSelectorDirectiveLoader,
    productSelectorControllerLoader) {
    'use strict';

    return function (module) {
        diProductSelectorDirectiveLoader(module);
        productSelectorControllerLoader(module);
    };
});


