define(function(){
    'use strict';

    return function(module){
        module.directive('diGrnDetails',diGrnDetails);

        function diGrnDetails(){
            return{
                restrict:'E',
                scope:{},
                controller:'GrnViewController',
                controllerAs:'grnController',
                templateUrl: 'src/app/portal/grn/grn-details/grn-details.html'
            }
        }
        return diGrnDetails;
    };
});


