define([
    './discover-parties-list.component',
    './discover-parties-list-configuration.service',
    './discovered-party-details.component'
], function (
    discoverPartiesListComponentLoader,
    discoverPartiesListConfigurationServiceLoader,
    discoveredPartyDetailsComponentLoader) {
        'use strict';

        return function (module) {
            module.component(
                discoverPartiesListComponentLoader.DiscoverPartiesListComponent.selector,
                discoverPartiesListComponentLoader.DiscoverPartiesListComponent);
            module.service('discoverPartiesListConfigurationService', discoverPartiesListConfigurationServiceLoader.DiscoverPartiesListConfigurationService);
            module.component(
                discoveredPartyDetailsComponentLoader.DiscoveredPartyDetailsComponent.selector,
                discoveredPartyDetailsComponentLoader.DiscoveredPartyDetailsComponent);
        };
    });


