define([
	'./diCurrencyFilter',
	'./currencyCache'
], function (
  currencyFilterLoader,
  currencyCacheLoader) {
    'use strict';

    return function (module) {
        currencyFilterLoader(module);
        currencyCacheLoader(module);
    };
});


