define([
    './chartSummaryController',
	'./chartSummaryDirective',
	'./chartConfigurationMapper',
	'./chartDataMapper',
	'./chartLayoutMapper',
	'./chartConfigMapper',
	'./chartFontMapper',
	'./chartTitleMapper'
], function (
    chartSummaryController,
    chartSummaryDirective,
    chartConfigurationMapper,
	chartDataMapper,
	chartLayoutMapper,
	chartConfigMapper,
	chartFontMapper,
	chartTitleMapper) {
    'use strict';

    return function (module) {
        chartSummaryController(module);
        chartSummaryDirective(module);
        chartConfigurationMapper(module);
        chartDataMapper(module);
        chartLayoutMapper(module);
        chartConfigMapper(module);
        chartFontMapper(module);
        chartTitleMapper(module);
    };
});


