// di.core.angular.controls.simpleselect
define([
    './diSimpleSelectDirective',
    './diSimpleSelectController'
], function (
    diSimpleSelectDirectiveLoader,
    diSimpleSelectControllerLoader) {
    'use strict';

    return function (module) {
        diSimpleSelectDirectiveLoader(module);
        diSimpleSelectControllerLoader(module);
    };
});


