class UserEditRestrictionsProxyService {
    constructor(webApiHttp, urlHelper) {
        this._webApiHttp = webApiHttp;
        this._urlHelper = urlHelper;
    }

    getEditRestrictions(invoiceId) {
        let requestedParams = {
            invoiceId: invoiceId
        };

        let requestedUrl = this._urlHelper.setParameters('Restrictions/GetEditRestrictions', requestedParams);

        return this._webApiHttp.get(requestedUrl);
    }
}

UserEditRestrictionsProxyService.prototype.constructor.$inject = ['webApiHttp', 'urlHelper'];

export { UserEditRestrictionsProxyService }



