define([
	'./authentication-client-event-handler.service',
	'./user-settings-store.service',
	'./user-rights'
], function (
	authenticationClientEventHandlerLoader,
	userSetingsStorageLoader,
	userRightsLoader) {
	'use strict';

	return function (module) {
		authenticationClientEventHandlerLoader(module);
		userSetingsStorageLoader(module);
		userRightsLoader(module);
	};
});


