// di.einvoicing.ui.portal.portal.summaries.components.snippets
define(function () {
	'use strict';

	return function (module) {
		var snippetContext = $di.angular.snippetRegistrar.createContext(
			module,
			'src/app/portal/summaries/_components/_snippets');

		snippetContext.registerSnippet(
			'diSummarySelector',
			'summarySelector.html',
			'SummarySelectorController',
			'summarySelectorCtrl');
	};
});


