const _columns = Symbol('columns');
const _tableOptions = Symbol('tableOptions');

class DiscoverPartiesListConfigurationService {
    constructor() {
        this.getTableOptions = this.getTableOptions.bind(this);

        this[_tableOptions] = {
            enableSelect: true,
            multiSelect: false,
            enablePaging: true,
            fixedHeight: true,
            maxItemsToShow: 500,
            paginationPageSize: 5,
            paginationPageNumber: 1,
            maxButtonsShown: 3
        };

        this[_columns] = [
            {
                field: 'nameAndFirstAddressLine',
                headerCellClass: "collapse",
                template:
                `<div>
                    <div>
                        <span>{{binding.addressBookEntry.party.name}}</span>
                    </div>
                    <div>
                        <span>{{binding.addressBookEntry.resource.line1 || binding.addressBookEntry.resource.country}}</span>
                    </div>                    
                </div>`
            }
        ];
    }

    getTableOptions() {
        let tableOptions = angular.copy(this[_tableOptions]);
        tableOptions.columns = this[_columns];
        return Promise.resolve(tableOptions);
    }
}

DiscoverPartiesListConfigurationService.prototype.constructor.$inject = [];

export { DiscoverPartiesListConfigurationService }


