define([
	'./_states',
    './lists/_loader',
    './viewer/_loader',
    './purchaseOrderRepository',
    './purchaseOrderLinesColumnSettings',
    './purchase-order-proxy.factory',
    './purchase-order-additional-document-references-configuration'
], function (
    statesLoader,
    listsLoader,
    viewerLoader,
	purchaseOrderRepositoryLoader,
    purchaseOrderLinesColumnSettingsLoader,
    purchaseOrderProxyLoader,
    additionalDocumentReferencesConfigurator) {
	'use strict';

	return function (module) {
		statesLoader(module);
		listsLoader(module);
		viewerLoader(module);
		purchaseOrderRepositoryLoader(module);
		purchaseOrderLinesColumnSettingsLoader(module);
		purchaseOrderProxyLoader(module);
	    additionalDocumentReferencesConfigurator(module);
	};
});



