define([
	'./tabularSummaryController',
	'./tabularSummaryDirective'
], function (
	tabularSummaryControllerLoader,
	tabularSummaryDirectiveLoader) {
	'use strict';

	return function (module) {
		tabularSummaryControllerLoader(module);
		tabularSummaryDirectiveLoader(module);
	};
});



