// di.einvoicing.ui.portal.portal.users.editor
define([
    './user-details.component',
    './user-details-configuration.service'

], function (
    userDetailsComponentLoader,
    userDetailsConfigurationServiceLoader) {
    'use strict';

    return function (module) {
        module.component(
                userDetailsComponentLoader.UserDetailsComponent.selector,
                userDetailsComponentLoader.UserDetailsComponent);

        module.service('userDetailsConfigurationService', userDetailsConfigurationServiceLoader.UserDetailsConfigurationService);
    };
});



