// di.core.angular.userSecurity
define([
	'./diChangePasswordDirective',
	'./diSetPasswordDirective',
	'./diForgotPasswordDirective',
	'./changePasswordController',
	'./setPasswordController',
	'./forgotPasswordController',
	'./userSecurityProxy',
	'./diPasswordPolicyDirective',
	'./diGetSecurityQuestionDirective',
	'./diSecurityAnswerDirective',
	'./diResetPasswordDirective',
	'./diResetPasswordSuccessDirective'
], function (
	changePasswordDirectiveLoader,
	setPasswordDirectiveLoader,
	forgotPasswordDirectiveLoader,
	changePasswordController,
	setPasswordController,
	forgotPasswordController,
	userSecurityProxy,
    passwordPolicyDirectiveLoader,
	getSecurityQuestionDirectiveLoader,
	securityAnswerDirectiveLoader,
	resetPasswordDirectiveLoader,
	resetPasswordSuccessDirectiveLoader
	) {
    'use strict';

    return function (module) {
        changePasswordDirectiveLoader(module);
        setPasswordDirectiveLoader(module);
        forgotPasswordDirectiveLoader(module);
        changePasswordController(module);
        setPasswordController(module);
        forgotPasswordController(module);
        userSecurityProxy(module);
        passwordPolicyDirectiveLoader(module);
        getSecurityQuestionDirectiveLoader(module);
        securityAnswerDirectiveLoader(module);
        resetPasswordDirectiveLoader(module);
        resetPasswordSuccessDirectiveLoader(module);
    };
});



