define(function () {
    'use strict';

    return function (module) {
        module.directive('diContactDetails', diContactDetails);

        function diContactDetails() {
            return {
                restrict: 'E',
                scope: {
                    bindToObject: "=",
                    onDataChanged: "="                    
                },                
                templateUrl: 'src/app/portal/invoices/editor/contact-details/contact-details.html'
            };
        }

        return diContactDetails;
    };
});


