define([
	'./rangeSelectorController',
	'./rangeSelectorDirective',
	'./summaryOptionsStore'
], function(
	rangeSelectorControllerLoader,
	rangeSelectorDirectiveLoader,
	summaryOptionsStoreLoader
	) {
	'use strict';

	return function (module) {
		rangeSelectorControllerLoader(module);
		rangeSelectorDirectiveLoader(module);
		summaryOptionsStoreLoader(module);
	};
});



