define([
	'./_states',
	'./audit-list/_loader',
    './audit-action-buttons/_loader'
], function (
	statesLoader,
	auditListsLoader,
    auditActionButtonsLoader) {
	'use strict';

	return function (module) {
		statesLoader(module);
		auditListsLoader(module);
	    auditActionButtonsLoader(module);
	};
});


