declare var $di: any;
declare var _: any;

let util = $di.utility.util;

import { LocationComponentController } from '../location.component.controller';

import { Component, Inject, Input, Output, EventEmitter, OnInit, OnChanges } from 'ng-metadata/core';

@Component({
    selector: 'diLocationList',
    templateUrl: 'src/app/portal/parties/locations/list/location-list.component.html'
})
export class LocationListComponent implements OnInit, OnChanges {
    @Input() boundToObject;
    @Input() tableOptions;
    @Input() sortOptions;
    @Input() readonly;
    @Input() selfManagement;
    @Input() partyCodes;
    @Input() selectedSortItem;

    @Output() onSortByChanged = new EventEmitter<any>();
    @Output() addNew = new EventEmitter<any>();
    @Output() update = new EventEmitter<any>();
    @Output() remove = new EventEmitter<any>();
    @Output() addPartyCode = new EventEmitter<any>();
    @Output() updatePartyCode = new EventEmitter<any>();
    @Output() deletePartyCode = new EventEmitter<any>();
    @Output() reloadData = new EventEmitter<void>();

    public defaultOptions;
    public parties;
    public locations;
    public sortBy;
    public selectedLocation;
    public typeOptions;
    public canAddNewAddress;
    public changed;
    public countries;
    public deliveryLocationIdentifiers;

    constructor(
        @Inject('CLIENT_ENUMS') private CLIENT_ENUMS,
        @Inject('enumTranslator') private enumTranslator,
        @Inject('modalWindow') private modalWindow,
        @Inject('staticLookupProxy') private staticLookupProxy,
        @Inject('translator') private translator,
        @Inject('$stateParams') private stateParams,
        @Inject('identifierFactory') private identifierFactory,
        @Inject('partyProxy') private partyProxy
    ) {
        this.countries = [];
        this.deliveryLocationIdentifiers = [];

        this.viewLocation = this.viewLocation.bind(this);
        this.sortByChanged = this.sortByChanged.bind(this);
    }

    ngOnInit() {
        this.staticLookupProxy.get('Country').then((data) => { this.countries.fill(data); });

        this.partyProxy.getEnabledSchemes().then((schemes) => {

            this.defaultOptions = this.identifierFactory.getDefaultOptions(
                schemes[this.CLIENT_ENUMS.partyCodeType.default],
                'deliveryLocationIdentifiers',
                'deliveryLocationIdentifier',
                this.setNewItem);
        });
    }

    ngOnChanges(changes) {
        if (changes.boundToObject && changes.boundToObject.currentValue) {
            this.translator.whenReady().then(() => {
                this.parties = changes.boundToObject.currentValue.parties;

                this.locations = _.map(changes.boundToObject.currentValue.items,
                    item => {
                        let location = _.extend({}, item);

                        location.classification = this.getClasification(location.isGlobal);

                        if (item && item.resource && item.resource.locationType) {
                            location.resource.locationTypeTranslated = this.enumTranslator.instantTranslate(this.CLIENT_ENUMS.locationType, item.resource.locationType);
                        }

                        if (item.resource.locationType === this.CLIENT_ENUMS.locationType.Accounting && location.isEditable) {
                            location.isAddressUpdateableOnly = true;
                        } else {
                            location.isAddressUpdateableOnly = false;
                        }

                        location.resource.identifier = this.identifierFactory.create(item.resource
                            .locationIdentifierScheme,
                            item.resource.locationIdentifierSchemeId,
                            item.resource.locationIdentifier);

                        return location;
                    });

                this.setAvailableAddressTypeOptionsToAdd();
            });
        }

        if (changes.sortOptions && changes.sortOptions.currentValue) {
            if (this.stateParams.locationSortOption) {
                this.sortBy = _.find(changes.sortOptions.currentValue,
                    (sortBy) => {
                        return sortBy.value === this.stateParams.locationSortOption;
                    });
            }
        }
    }

    getClasification(isGlobal) {
        if (isGlobal) {
            return this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.TABLE.PUBLIC');
        } else {
            return this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.TABLE.PRIVATE');
        }
    }

    sortByChanged(value) {
        if (this.sortBy && this.sortBy.value === value) {
            return;
        }

        this.tableOptions.paginationPageNumber = 1;
        this.onSortByChanged.emit(value);
    }

    countryChanged(country) {
        this.selectedLocation.country = country;
        this.selectedLocation.countryId = country.id;
    }

    viewLocation(obj) {
        obj.party.partyId = obj.party.id;
        obj.party.name = obj.resource.locationTypeTranslated;
        obj.party.locationType = obj.resource.locationType;
        obj.resource.party = obj.party;


        let identifiers = this.defaultOptions;
        obj.identifiers = _.clone(identifiers);

        let exists = _.find(obj.identifiers,
            (item) => {
                return item.identifier === obj.resource.identifier.identifier;
            });

        if (util.isNullOrUndefined(exists)) {
            obj.identifiers.push(obj.resource.identifier);
        }

        obj.partyCodes = this.setObjectIdentifiers(this.partyCodes.items);

        const options = {
            controller: LocationComponentController,
            templateUrl: 'src/app/portal/parties/locations/location.component.html',
            model: obj,
            addPartyCode: this.addPartyCode,
            updatePartyCode: this.updatePartyCode,
            deletePartyCode: this.deletePartyCode,
            onDataChanged: this.locationChanged,
            onCountryChanged: this.countryChanged,
            title: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.' + (obj.isEditable ? 'EDIT' : 'VIEW')),
            save: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.SAVE'),
            cancel: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.CANCEL'),
            remove: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.REMOVE')
        }

        this.showPopup(options);
    }

    setObjectIdentifiers(items) {
        let partyCodes: any = {};

        partyCodes.defaultIdentifiers = this.setIdentifiers(items, this.CLIENT_ENUMS.partyCodeType.default);

        partyCodes.endpointIdentifiers = this.setIdentifiers(items, this.CLIENT_ENUMS.partyCodeType.endpoint);

        partyCodes.taxIdentifiers = this.setIdentifiers(items, this.CLIENT_ENUMS.partyCodeType.taxReference);

        return partyCodes;
    }

    setIdentifiers(items, identifierType) {
        return _.chain(items)
            .filter((item) => {
                return item.code.partyCodeType === identifierType;
            })
            .map((item) => {
                return {
                    id: item.code.id,
                    code: item.code.codeValue,
                    relatedParty: item.code.relatedPartyId,
                    scheme: item.code.scheme,
                    schemeId: item.code.schemeId,
                    type: item.code.partyCodeType
                };
            })
            .value();
    }

    setAvailableAddressTypeOptionsToAdd() {
        this.typeOptions = [];

        var isGlobal = this.selfManagement;

        let businessLocation = _.find(this.locations,
            location => location.isGlobal === isGlobal && location.resource.locationType === this.CLIENT_ENUMS.locationType.Business);

        let businessAdded = util.isDefined(businessLocation);

        let accountingLocation = _.find(this.locations,
            location => location.isGlobal === isGlobal && location.resource.locationType === this.CLIENT_ENUMS.locationType.Accounting);

        let accountingAdded = util.isDefined(accountingLocation);

        let index = 1;

        const addTypeOption = (party, locationType) => {
            let partyClone = _.clone(party);

            partyClone.partyId = party.id;
            partyClone.id = index++;
            partyClone.name = this.enumTranslator.instantTranslate(this.CLIENT_ENUMS.locationType, locationType);
            partyClone.locationType = locationType;
            this.typeOptions.push(partyClone);            
        };

        _.each(this.parties,
            party => {
                if (party.isAccountingCustomer) {
                    if (accountingAdded === false) {
                        addTypeOption(party, this.CLIENT_ENUMS.locationType.Accounting);

                        accountingAdded = true;
                    }
                } else {
                    if (party.isBuyer) {
                        addTypeOption(party, this.CLIENT_ENUMS.locationType.Delivery);
                    }

                    if (businessAdded === false) {
                        addTypeOption(party, this.CLIENT_ENUMS.locationType.Business);

                        businessAdded = true;
                    }
                }
            });

        this.canAddNewAddress = this.typeOptions.length > 0;
    }

    addLocation() {
        if (this.typeOptions.length > 0) {
            this.selectedLocation = {
                country: '',
                countryCode: '',
                countryId: 0,
                isUpdatable: true
            }

            this.selectedLocation.locationTypeTranslated = this.typeOptions[0].name;
            this.selectedLocation.locationType = this.typeOptions[0].locationType;
            this.selectedLocation.party = this.typeOptions[0];

            let defaultOptions = this.defaultOptions;

            let identifiers = defaultOptions;

            const options = {
                controller: LocationComponentController,
                templateUrl: 'src/app/portal/parties/locations/location.component.html',
                model: {
                    isNew: true,
                    isEditable: true,
                    resource: this.selectedLocation,
                    allowedTypes: this.typeOptions,
                    identifiers: identifiers,
                    party: this.typeOptions[0],
                    partyCodes: this.setObjectIdentifiers(this.partyCodes.items)                    
                },
                onDataChanged: this.locationChanged,
                onCountryChanged: this.countryChanged,
                title: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.LOCATION.ADD'),
                save: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.SAVE'),
                cancel: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.CANCEL'),
                remove: this.translator.instantTranslate('EINVOICING.PORTAL.PARTIES.LOCATIONS.LIST.DELETE')
            }

            this.showPopup(options);
        }
    }

    getCountryById(id) {
        let country = _.find(this.countries,
            (country) => {
                return country.id === id;
            });

        if (util.isUndefined(country)) {
            country = {};
        }

        return country;
    }

    locationChanged() {
        this.changed = true;
    }

    setNewItem(item, arrayName) {
        this[arrayName].splice(2, 0, item);
    }

    showPopup(options) {
        this.modalWindow.show(options,
            (result) => {
                this.locationSaved(result);
            },
            () => {
                if (this.changed && this.reloadData) {
                    this.reloadData.emit(null);
                }
            },
            (result) => {
                this.locationDeleted(result);
            });
    }

    locationSaved(location) {
        const resource = location.resource;

        const existing = _.find(this.locations,
            (item) => {
                return item.resource.id === resource.id;

            });

        if (util.isUndefined(existing) === false) {
            if (this.update) {
                this.update.emit(resource);
            }

        } else {
            if (this.addNew) {
                this.addNew.emit(resource);
            }
        }
    }

    locationDeleted(location) {
        const resource = location.resource;

        if (this.remove) {
            this.remove.emit(resource);
        }
    }
}


