//{CODE:CAC02}
// di.core.angular.controls
define([
	'./button/_loader',
	'./image/_loader',
	'./input/_loader',
	'./diTranscludeRepeatDirective',
	'./diTranscludeReplaceDirective',
	'./elementNotifier',
	'./elementNotifierStyles',
	'./notifier',
	'./promptWindow/_loader',
	'./modal/_loader',
	'./spinner/_loader',
	'./toggle/_loader',
	'./select/_loader',
	'./simpleSelect/_loader',
	'./multiSelect/_loader',
	'./diEnabledDirective',
	'./diClickDirective',
	'./dataTable/_loader',
	'./gridPaginationButtons/_loader',
	'./gridPaginationPageSize/_loader',
	'./enterKey/_loader',
    './chart/_loader',
	'./diNoDirtyDirective',
    './restrictions/_loader'
], function (
	buttonDirectiveLoader,
	imageDirectiveLoader,
	inputLoader,
	transcludeRepeatDirectiveLoader,
	transcludeReplaceDirectiveLoader,
    elementNotifierLoader,
	elementNotifierStylesLoader,
    notifierLoader,
	promptWindowLoader,
	modalLoader,
	spinnerLoader,
	toggleLoader,
	selectLoader,
	simpleSelectLoader,
	multiSelectLoader,
	diEnabledLoader,
	diClickLoader,
    diDataTableLoader,
    gridPaginationButtonsLoader,
    gridPaginationPageSizeLoader,
    enterKeyLoader,
    chartLoader,
	diNoDirtyLoader,
    restrictionsLoader) {
    'use strict';

    return function (module) {
        buttonDirectiveLoader(module);
        imageDirectiveLoader(module);
        inputLoader(module);
        transcludeRepeatDirectiveLoader(module);
        transcludeReplaceDirectiveLoader(module);
        elementNotifierLoader(module);
        elementNotifierStylesLoader(module);
        notifierLoader(module);
        promptWindowLoader(module);
        modalLoader(module);
        spinnerLoader(module);
        toggleLoader(module);
        selectLoader(module);
        simpleSelectLoader(module);
        multiSelectLoader(module);
        diEnabledLoader(module);
        diClickLoader(module);
        diDataTableLoader(module);
        gridPaginationButtonsLoader(module);
        gridPaginationPageSizeLoader(module);
        enterKeyLoader(module);
        chartLoader(module);
        diNoDirtyLoader(module);
        restrictionsLoader(module);
    };
});



