// di.core.angular.exception
define([
    './_config',
    './exceptionHandler',
    './exceptionHttpInterceptor',
    './stackTrace'
], function (
    configLoader,
    exceptionHandlerLoader,
    exceptionHttpInterceptorLoader,
    stackTraceLoader
    ) {
    'use strict';

    return function (module) {
        exceptionHandlerLoader(module);
        exceptionHttpInterceptorLoader(module);
        stackTraceLoader(module);

        configLoader(module);
    };
});



