define(function () {
	'use strict';

	return function (module) {
		module.directive('diNoDirty', diNoDirty);

		function diNoDirty() {
			return {
				restrict: 'A',
				require: "ngModel",
				link: link
			}

			function link(scope, element, attrs, ngModelCtrl) {
				//angular.noop is a function that performs no operations
				ngModelCtrl.$setDirty = angular.noop;
			}
		}

		return diNoDirty;
	};
});


