define([
    './product-type-selector.directive',
    './product-type-selector.controller'
], function (
    diProductTypeSelectorDirectiveLoader,
    productTypeSelectorControllerLoader) {
    'use strict';

    return function (module) {
        diProductTypeSelectorDirectiveLoader(module);
        productTypeSelectorControllerLoader(module);
    };
});


