//{CODE:CAC0X}
// di.core.angular.controls.button
define(function() {
	'use strict';

	return function(module) {
		var util = $di.utility.util;
		module.directive('diButton', diButton);

		function diButton() {
			return {
				restrict: 'E',
				template: template,
				controller: 'DiButtonController',
				controllerAs: 'diButtonCtrl',
				replace: false,
				transclude: false,
				scope: {
					tooltip: '@',
					validate: '@',
					prompt: '@',
					promptCondition: "=",
					promptTitle: '@',
					promptMessage: '@',
					promptOkBtnText: '@',
					promptCancelBtnText: '@',
					prePromptAction: '&?',
					action: '&',
					image: '@',
					imageColour: '@',
					enabled: '@',
					btnClass: '@',
					btnDisabledClass: '@',
					filled: '@',
					text: '@',
					diToggleTrigger: '@'
				}
			};

			/**
			* This should be placed in a seperate .html file - we need to provide deployment solution for this?
			* Perhaps npm - gulp-angular-templatecache?
			*/
			function template(element, attrs) {
				var html =
'<div\
	ng-if="text !== undefined"\
	class="btn-container"\
	ng-class="tooltip ? \'tooltip-wrap\' : \'\'"\>\
	<button\
		id="btn"\
		type="button"\
		ng-class="[\'center\', btnClass ? btnClass : \'btn small\', image ? \'center iconed\' : \'\', filled ? \'\' : \'\']"\
		di-click="diButtonCtrl.clicked()">\
		<di-image\
			ng-if="image !== undefined"\
			image-colour="{{::imageColour}}"\
			image="{{::image}}"\
			class="icon">\
		</di-image>\
		<span\
			id="span"\
			di-translate="{{::text}}">\
		</span>\
	</button>\
	<div\
		id="btnDisabled"\
		style="display: none"\
		class="outline"\
		ng-class="[btnDisabledClass ? btnDisabledClass : btnClass ? btnClass : \'btn-disabled small\', image ? \'center iconed\' : \'\']">\
		<di-image\
			ng-if="image !== undefined"\
			id="imageDisabled"\
			image-colour="offWhite"\
			image="{{::image}}"\
			class="icon">\
		</di-image>\
		<span\
			id="spanDisabled"\
			di-translate="{{::text}}">\
		</span>\
	</div>\
	<div\
		ng-if="tooltip"\
		id="tooltip"\
		class="tooltip">\
		<span\
			di-translate="{{::tooltip}}"></span>\
	</div>\
</div>\
<div\
	ng-if="text === undefined"\
	class="btn-container"\
	ng-class="tooltip ? \'tooltip-wrap\' : \'\'">\
	<button\
		id="btn"\
		type="button"\
		ng-class="btnClass ? btnClass : \'btn-icon\'"\
		di-click="diButtonCtrl.clicked()">\
		<di-image\
			ng-if="image !== undefined"\
			id="image"\
			image-colour="{{::imageColour}}"\
			class="icon"\
			image="{{::image}}">\
		</di-image>\
		<span\
			id="span"\
			di-translate="{{::text}}">\
		</span>\
	</button>\
	<div\
		id="btnDisabled"\
		style="display: none"\
		ng-class="btnDisabledClass ? btnDisabledClass : btnClass ? btnClass : \'btn-icon-disabled\'">\
		<di-image\
			ng-if="image !== undefined"\
			id="imageDisabled"\
			image-colour="offWhite"\
			class="icon"\
			image="{{::image}}">\
		</di-image>\
		<span\
			id="spanDisabled">\
		</span>\
	</div>\
	<div\
		ng-if="tooltip"\
		id="tooltip"\
		class="tooltip">\
		<span\
			di-translate="{{::tooltip}}"></span>\
	</div>\
</div>';

				if (util.isDefined(attrs.validate)) {
					html = html.replace('di-click', 'di-validate-click');
				}

				return html;
			}
		}

		return diButton;
	};
});


