define([
	'./_states',
    './user-list/_loader',
    './user-details/_loader',
    './user-bottom-buttons/_loader',
    './user-main-buttons/_loader',
    './user-details-header/_loader',
    './users-action-buttons/_loader',
    './user-edit-restrictions/_loader'
], function (
    statesLoader,
    listsLoader,
    userDetailsLoader,
    usersBottomButtonsLoader,
    usersMainButtonsLoader,
    userDetailsHeaderLoader,
    usersActionsButtonsLoader,
    userEditRestrictionsLoader
    ) {
    'use strict';

    return function (module) {
        statesLoader(module);
        listsLoader(module);
        userDetailsLoader(module);
        usersBottomButtonsLoader(module);
        usersMainButtonsLoader(module);
        userDetailsHeaderLoader(module);
        usersActionsButtonsLoader(module);
        userEditRestrictionsLoader(module);
    };
});



