export const PartyCodesComponent = {
    templateUrl: 'src/app/portal/parties/party-codes/party-codes.component.html',
    selector: 'diPartyCodes',
    bindings: {
        readonly: '<',
        partyId: '<',
        identifiers: '<',
        endpoints: '<',
        taxReferences: '<',
        addNewCode: '&',
        updatePartyCode: '&',
        deletePartyCode: '&',
    },
    controllerAs: 'partyCodesCtrl',
    controller:
    class PartyCodesController {

        constructor(partyProxy) {
            this.partyProxy = partyProxy;

            this.addNewPartyCode = this.addNewPartyCode.bind(this);
            this.updateCode = this.updateCode.bind(this);
        }

        $onInit() {
            this.partyProxy.getAssociatedPartiesTradingRelationshipsByParty(this.partyId).then((parties) => {
                this.relatedParties = parties;
                
                //only working for identifiers ATM
                this.identifiers = _.map(this.identifiers, (partyCode) => {
                    if(partyCode.relatedParty > 0) {
                        var party = _.find(this.relatedParties, (relatedParty) => {
                            return partyCode.relatedParty === relatedParty.id;
                        });

                        partyCode.relatedPartyDetails = party;
                    }

                    return partyCode;
                });
            });
        }

        addNewPartyCode(data) {
            data.partyId = this.partyId;
            this.addNewCode({ data });
        }

        updateCode(data) {
            data.partyId = this.partyId;            
            this.updatePartyCode({data});
        }

        deleteCode(data) {
            data.partyId = this.partyId;            
            this.deletePartyCode({data});
        }
    }
}

PartyCodesComponent.controller.prototype.constructor.$inject = ['partyProxy']



