//{CODE:EUP02}
// di.einvoicing.ui.portal
define([
    'angular',
    // Application Angular components
    'appConfig',
    './_run',
    './_states',
	'./shared/components/_loader',
	'./portal/_loader',
	'./welcome/_loader',
	'./impersonate/_loader',
	'../../lib/di.core/di.core.spa',
    // External Angular components
	'angular-animate',
    'angular-ui-router',
    'angular-translate',
    'angular-translate-loader-static-files',
    'angular-local-storage',
	'valdr',
	'angular-sanitize',
	'ng-file-upload',
    'ngIdle',
    'angularModalService',
    'angular-clipboard'
], function (
    ng,
    configLoader,
    runLoader,
    statesLoader,
    componentsLoader,
    portalLoader,
	welcomeLoader,
	impersonateLoader,
    diCoreAngularLoader) {
	'use strict';

	var module = ng.module('di.einvoicing.ui.portal', [
		'ngAnimate',
		'ui.router',
        'pascalprecht.translate',
        'LocalStorageModule',
		'valdr',
		'ngSanitize',
		'ngFileUpload',
        'ngIdle',
        'angularModalService',
        'angular-clipboard'
	]);

	configLoader(module);
	runLoader(module);
	componentsLoader(module);
	portalLoader(module);
	welcomeLoader(module);
	impersonateLoader(module);
	statesLoader(module);

	diCoreAngularLoader(module);

	return module;
});



