define([
	'./party-proxy.factory',
	'./user-proxy.factory',
	'./audit-proxy.factory',
	'./document-proxy.factory',
	'./formatters/_loader',
	'./attachment-proxy.factory',
	'./file-transfer/_loader',
	'./attachment-download-validator/_loader',
	'./user-actions-resolver/_loader',
	'./vm-list-configurator/_loader',
	'./notification-handler/_loader',
	'./document-attachment-configurator/_loader',
	'./trading-relationship-proxy',
    './proxy-error-interceptor/proxy-error-interceptor.service'
], function (
	partyProxyLoader,
	userProxyLoader,
	auditProxyLoader,
	documentProxyLoader,
	formattersLoader,
	attachmentProxyLoader,
	fileTransferLoader,
	attachmentDownloadValidatorLoader,
	userActionsResolverLoader,
	vmListConfiguratorLoader,
	notificationHandlerLoader,
	documentAttachmentConfiguratorLoader,
	tradingRelationshipProxyLoader,
    proxyErrorInterceptorService) {
		'use strict';

		return function (module) {
			partyProxyLoader(module);
			userProxyLoader(module);
			auditProxyLoader(module);
			documentProxyLoader(module);
			formattersLoader(module);
			attachmentProxyLoader(module);
			fileTransferLoader(module);
			attachmentDownloadValidatorLoader(module);
			userActionsResolverLoader(module);
			vmListConfiguratorLoader(module);
			notificationHandlerLoader(module);
			documentAttachmentConfiguratorLoader(module);
			module.service('tradingRelationshipProxy', tradingRelationshipProxyLoader.TradingRelationshipProxy);
			module.service('proxyErrorInterceptor', proxyErrorInterceptorService.ProxyErrorInterceptor);
		};
	});


