// di.core.angular.validation
define([
	'./_config',
	'./comparerRegistrar',
	'./defaultComparer',
	'./momentComparer'
], function (
	configLoader,
	comparerRegistrarLoader,
	defaultComparerLoader,
	momentComparerLoader
	) {
	'use strict';

	return function (module) {
	    configLoader(module);
	    comparerRegistrarLoader(module);
	    defaultComparerLoader(module);
	    momentComparerLoader(module);
	};
});



