import { Component, Inject, Injectable } from 'ng-metadata/core';
import { ModalControllerBase } from '../../../../../lib/di.core/spa/controls/modal/modal-base.controller';

@Injectable()
export class LocationComponentController extends ModalControllerBase {
    public model;
    public title;
    public save;
    public cancel;
    public remove;
    public options;
    public partyCodeReadonly;

    constructor(
        @Inject('$element') $element,
        @Inject('promptWindow') promptWindow,
        @Inject('options') options,
        @Inject('close') close
    ) {
        super($element, promptWindow, options, close);

        this.model = options.model;
        this.title = options.title;
        this.save = options.save;
        this.cancel = options.cancel;
        this.remove = options.remove;
        this.options = options;

        this.partyCodeReadonly = false;

        if (this.model.isEditable && this.model.isGlobal) {
            this.partyCodeReadonly = true;
        }
    }

    addPartyCode(data) {
        this.options.addPartyCode.emit(data);
    }

    updatePartyCode(data) {
        this.options.updatePartyCode.emit(data);
    }

    deletePartyCode(data) {
        this.options.deletePartyCode.emit(data);
    }

    onCountryChanged(country) {
        this.changed = true;

        if (this.options.onCountryChanged) {
            this.options.onCountryChanged(country);
        }

        this.onDataChanged();
    }
}
LocationComponentController.prototype.constructor.$inject = ['$element', 'promptWindow', 'options', 'close']


