let util = $di.utility.util;

export default class SetFocus {
  constructor() {
    this.restrict = 'A';
  }
  link($scope, $element, $attrs) {
    const setFocusOn = $element[0].querySelector($attrs.diSetFocus);
      
    if (setFocusOn !== null && setFocusOn != undefined){
        if (util.isDefined($attrs.diSetFocusParent)) {
            setFocusOn.parentNode.focus();
        } else {
            setFocusOn.focus();
        }
    } else {
        $element[0].focus();
    }
  }
}


