define([
    './_snippets/_loader',
	'./optionalFields/_loader',
    './invoiceDetails/invoiceDetailsDirective'    
], function (
    snippetsLoader,
	optionalFieldsLoader,
    invoiceDetailsLoader) {
    'use strict';

    return function (module) {
        snippetsLoader(module);
	    optionalFieldsLoader(module);
	    invoiceDetailsLoader(module);	    
    };
});


