// di.core.angular.controls.chart
define([
    './diChartController',
	'./diChartDirective',
	'./chartPlotter'
], function (
    diChartController,
    diChartDirective,
    chartPlotter) {
    'use strict';

    return function (module) {
        diChartController(module);
        diChartDirective(module);
        chartPlotter(module);
    };
});


