define([
	'./_states',
	'./_components/_loader',
    './shared/_loader',
	'./editor/_loader',
	'./lists/_loader',
	'./viewer/_loader',
	'./invoiceRepository',
    './unit-of-measure-selector/_loader',
    './allowances-charges/_loader',
    './document-issues/_loader',
    './attachment-uploader/_loader',
    './product-selector/_loader',
    './product-type-selector/_loader',
    './tax-code-selector/_loader',
    './product-lines/_loader',
    './invoice-proxy.factory',
    './product-proxy.factory',
    './tax-proxy.factory'
], function(
	statesLoader,
	componentsLoader,
    sharedLoader,
	editorLoader,
	listsLoader,
	viewerLoader,
	invoiceRepositoryLoader,
    unitOfMeasureSelectorLoader,
    allowancesChargesLoader,
    documentIssuesLoader,
    attachmentsLoader,
    productSelectorLoader,
    productTypeSelectorLoader,
    taxCodeSelectorLoader,
    productLinesLoader,
    invoiceProxyLoader,
    productProxyLoader,
    taxProxyLoader) {
	'use strict';

	return function(module) {
	    invoiceProxyLoader(module);
	    productProxyLoader(module);
	    taxProxyLoader(module);
	    statesLoader(module);
	    componentsLoader(module);
	    sharedLoader(module);
		editorLoader(module);
		listsLoader(module);
		viewerLoader(module);
		invoiceRepositoryLoader(module);
		unitOfMeasureSelectorLoader(module);
		allowancesChargesLoader(module);
		documentIssuesLoader(module);
		attachmentsLoader(module);
		productSelectorLoader(module);
		productTypeSelectorLoader(module);
		taxCodeSelectorLoader(module);
	    productLinesLoader(module);
	};
});


