define([
    './diButtonController',
	'./diButtonDirective'
], function (
    buttonContrllerLoader,
    buttonDirectiveLoader) {
    'use strict';

    return function (module) {
        buttonContrllerLoader(module);
        buttonDirectiveLoader(module);
    };
});


