define([
	'./grn-search/_loader',
    './grn-list-configurator.provider',
    './grn-list.controller',
    './grn-list.directive'
], function(
	grnSearchLoader,
    grnListConfigurator,
    grnListController,
    grnListDirective) {
	'use strict';

	return function(module) {
	    grnSearchLoader(module);
	    grnListConfigurator(module);
	    grnListController(module);
	    grnListDirective(module);
	};
});


