// di.core.angular.http
define([
    './dHttp',
	'./webApiHttp',
	'./authHttp',
	'./serverResponseHandler'
], function (
    dHttpLoader,
	webApiHttpLoader,
    authHttpLoader,
	serverResponseHandlerLoader) {
    'use strict';

    return function (module) {
        dHttpLoader(module);
        webApiHttpLoader(module);
        authHttpLoader(module);
	    serverResponseHandlerLoader(module);
    };
});



