let util = $di.utility.util;

export const AttachmentUploaderComponent = {
    templateUrl: 'src/app/portal/invoices/attachment-uploader/attachment-uploader.component.html',
    selector: 'diAttachmentUploader',
    bindings: {
        tabIndex: '@',
        bindToObject: "<",
        attachmentDataId: "<",
        fileName: "<",
        download: '&',
        onUploadStateChanged: '&'
    },
    controller: class {
        constructor(
        $scope,
        $element,
        documentAttachmentConfigurator,
        attachmentProxy,
        notificationHandler,
        elementNotifier,
        translator,
        fileExtensionValidator
        ) {
            this.$scope = $scope;
            this.$element = $element;
            this.documentAttachmentConfigurator = documentAttachmentConfigurator;
            this.attachmentProxy = attachmentProxy;
            this.notificationHandler = notificationHandler;
            this.elementNotifier = elementNotifier;
            this.translator = translator;
            this.fileExtensionValidator = fileExtensionValidator;

            this.showSpinner = false;
            
            this.onFileSelected = this.onFileSelected.bind(this);
        }
        
        $onInit() {
            this.removeSpinner();
            
            this.maxSize = this.documentAttachmentConfigurator.getMaximumAttachmentSize();

            this.$scope.$watch(() => { return this.invalidFile }, this.setLanguageFailureKey());
        }

        $onChanges(changes) {
            if (util.isNullOrUndefined(changes.fileName.currentValue) ||
                util.isNullOrUndefined(changes.attachmentDataId.currentValue)) {

                this.setUploadButtonVisibility(true);
                this.selectedFileName = undefined;
                this.selectedAttachmentDataId = undefined;
            } else {
                this.setUploadButtonVisibility(false);
                this.selectedFileName = changes.fileName.currentValue;
                this.selectedAttachmentDataId = changes.attachmentDataId.currentValue;
            }
        }
        
        onFileSelected() {
            if (util.isObject(this.file)) {
                this.showUploadButton = false;
                this.showFileName = false;

                if (this.fileExtensionValidator.isFileExtensionValid(this.file.name) === false) {
                    this.notificationHandler.showInfo('EINVOICING.PORTAL.COMPONENTS.ATTACHMENTS.INVALID_FILE_TYPE');
                    this.showUploadButton = true;
                    return;
                };

                this.onUploadStarted();

                this.addSpinner();

                this.hideUploadErrorNotification();

                this.attachmentProxy.addAttachmentData(this.file).then((data) => {
                        this.setUploadButtonVisibility(false);

                        this.selectedFileName = this.file.name;
                        this.selectedAttachmentDataId = data;
                        this.onUploadSuccessful();
                    },
                    () => {
                        this.setUploadButtonVisibility(true);

                        this.ctrl.selectedFileName = undefined;
                        this.ctrl.selectedAttachmentDataId = undefined;

                        this.onUploadFailed();
                    }).finally(() => {
                    this.file = undefined;
                    this.removeSpinner();
                });
            }
        }
        
        onUploadStarted() {
            this.handleUploadStateChange({
                lineIdentifier: this.bindToObject,
                status: 'started'
            });
        }
        
        onUploadSuccessful() {
            this.handleUploadStateChange({
                lineIdentifier: this.bindToObject,
                status: 'success',
                attachmentDataId: this.selectedAttachmentDataId,
                filename: this.selectedFileName
            });
        }
        
        onUploadFailed() {
            this.handleUploadStateChange({
                lineIdentifier: this.bindToObject,
                status: 'failed'
            });
        }

        handleUploadStateChange(state) {
            if (util.isFunction(this.onUploadStateChanged)) {
                this.onUploadStateChanged(state);
            }
        }

        /**
         * event handler when file selection fails         
         */
        setLanguageFailureKey() {
            return () => {
                if (util.isObject(this.invalidFile)) {
                    const failureReason = this.invalidFile.$error.replace(/([A-Z])/g, "_$1").toUpperCase();

                    const errorKey = 'EINVOICING.PORTAL.COMPONENTS.ATTACHMENTS.FILE_ERRORS.' + failureReason;

                    const translationArgs = {
                        maxSize: this.maxSize
                    }

                    this.translator.translate(errorKey, translationArgs).then((translated) => {
                        this.uploadErrorNotification = this.elementNotifier.error(
                            translated,
                            this.$element,
                            { clickToHide: true });

                        this.uploadErrorNotification.show();
                    });
                }
            }
        }

        /**
         * hides error notification        
         */
        hideUploadErrorNotification() {
            if (util.isDefined(this.uploadErrorNotification)) {
                this.uploadErrorNotification.destroy();
                this.uploadErrorNotification = undefined;
            }
        }
        
        setUploadButtonVisibility(visibility) {
            this.showUploadButton = visibility;
            this.showFileName = !visibility;
        }

        /**
         * adds spinner to control         
         */
        addSpinner() {
            this.showSpinner = true;
        }

        /**
         * removes spinner         
         */
        removeSpinner() {
            this.showSpinner = false;
        }
    }
}

AttachmentUploaderComponent.controller.prototype.constructor.$inject = [
    '$scope',
    '$element',
    'documentAttachmentConfigurator',
    'attachmentProxy',
    'notificationHandler',
    'elementNotifier',
    'translator',
    'fileExtensionValidator'
];


