export const UserDetailsHeaderComponent = {
    selector: 'diUserDetailsHeader',
    bindings: {
        mode:'<',
        user: '<',
        allRoles:'<',
        selectedRoleId: '=',
        allWorkingFor: '<',
        isSupervisor: '<',
        labels: '<',
        allocatedPartyName: '<',
        letterRangeFilter: '<',
        unallocated: '<',
        selectedRestriction: '<',
        allRights: '<',
        onChanged: '&',
        onRoleChanged: '&',
        onWorkingChanged: '&',
        onSearchRangeChanged: '&',
        filterParties: '&',
        subscribeToEvents: '&'
    },
    controller: class {
        constructor(translator) {
            this._translator = translator;
        }

        $onInit() {
            if (this.subscribeToEvents) {
                this.subscribeToEvents({ subscription: this.setRights.bind(this) });
            }
        }

        setRights(rights) {
            this.allRights = rights.allRights;
            
            let roleRights = rights.roleRights;
            let userRights = rights.userRights;
            
            let selectedRoleRights = roleRights.find(x=>x.role.id === this.selectedRoleId).rights;

            for (let right of this.allRights) {
                right.friendlyName = this._translator.instantTranslate('EINVOICING.PORTAL.USERS.EDITOR.RIGHTS.NAME.' + right.id);
                right.description = this._translator.instantTranslate('EINVOICING.PORTAL.USERS.EDITOR.RIGHTS.DESCRIPTION.' + right.id);

                let rightFoundInRole = selectedRoleRights.find(x => x.id === right.id);
                let rightFoundInUser = userRights.find(x => x.id === right.id);
                
                if (rightFoundInRole && rightFoundInUser) {
                    right.selected = true;
                } else if (rightFoundInUser && !rightFoundInRole) {
                    right.selected = true;
                } else {
                    right.selected = false;
                }
            }
        }
    },
    templateUrl: 'src/app/portal/users/user-details-header/user-details-header.component.html'
}

UserDetailsHeaderComponent.controller.prototype.constructor.$inject = ['translator'];


