define([
	'./components/_loader',
	'./services/_loader',
    './services/button-state-resolver/_loader',
    './services/document-attribute-resolver/_loader',
    './services/file-extension-validator/_loader',
    './services/document-type-resolver/_loader'
], function(
	componentsLoader,
	servicesLoader,
    buttonStateResolverLoader,
    documentAttributeResolverLoader,
    fileExtensionValidatorLoader,
    documentTypeResolverLoader) {
	'use strict';

	return function(module) {
		componentsLoader(module);
		servicesLoader(module);
		buttonStateResolverLoader(module);
		documentAttributeResolverLoader(module);
		fileExtensionValidatorLoader(module);
	    documentTypeResolverLoader(module);
	};
});



