// di.core.angular.controls.spinner
define([
	'./spinnerGenerator',
	'./modalSpinner'
], function (
	spinnerGeneratorLoader,
	modalSpinnerLoader
    ) {
	'use strict';

	return function (module) {
		spinnerGeneratorLoader(module);
		modalSpinnerLoader(module);
	};
});



