// di.einvoicing.ui.portal.portal.related.lists
define([
	'./_components/_loader',
	'./relatedListController',
	'./relatedListConfigurator'
], function(
	componentsLoader,
	relatedListControllerLoader,
	relatedListConfiguratorLoader) {
	'use strict';

	return function(module) {
		componentsLoader(module);
		relatedListControllerLoader(module);
		relatedListConfiguratorLoader(module);
	};
});



