class AccountService {
    constructor(userProxy, authenticationData) {
        this.userProxy = userProxy;
        this.authenticationData = authenticationData;
        
        this.currentUser = {};
        this.notifiableEvents = [];
    }

    getUser(success, error) {
        let promise = this.userProxy.getById(this.authenticationData.userData.userId);
        promise.then(data => {
                this.currentUser = data.user;
                this.notifiableEvents = data.notifiableEvents;
                success();
            },
            (message) => {
                if (message && message.failureReason) {
                    error(message.failureReason);
                } else {
                    error();
                }
            });
    }
}

AccountService.prototype.constructor.$inject = ['userProxy', 'authenticationData'];

export { AccountService }


