// di.einvoicing.ui.portal._components._snippets
define(function () {
	'use strict';

	return function (module) {
		var snippetContext = $di.angular.snippetRegistrar.createContext(
			module,
			'src/app/shared/components/header/_snippets');

		snippetContext.registerSnippet('diHeader', 'header.html', 'HeaderController', 'headerCtrl');
	};
});


