import { Injectable, Inject } from 'ng-metadata/core';

import { ResourceHandlerServiceBase } from '../../shared/resource-handler-base.service';

@Injectable()
export class ContactHandlerService extends ResourceHandlerServiceBase {
    public type: string = "contact";

    constructor(
        @Inject('contactProxy') private contactProxy,
        @Inject('contactListConfigurationService') private contactListConfigurationService
    ) {
        super(contactListConfigurationService);
    }
    
    getResourceBook(partyId, forPartyId, sortBy = 1) {
        return this.contactProxy.getContactBook(partyId, forPartyId, (sortBy || {}) || sortBy);
    }

    addNewResource(resource, forPartyId = null){
        return this.contactProxy.addContactBookEntry(resource, forPartyId);
    }

    updateResource(resource, resourceId) {
        return this.contactProxy.updateContactBookEntry(resource, resourceId);
    }

    removeResource(resource, resourceId) {
        return this.contactProxy.removeContactBookEntry(resource, resourceId);
    }
}

ContactHandlerService.prototype.constructor.$inject = ['contactProxy', 'contactListConfigurationService'];


