define([
	'./_snippets/_loader',
	'./relatedList/relatedListDirective'
], function(
	snippetsLoader,
	relatedListDirectiveLoader) {
	'use strict';

	return function(module) {
		snippetsLoader(module);
		relatedListDirectiveLoader(module);
	};
});


