// di.einvoicing.ui.portal.components
define([
    './language-selector.directive',
    './language-selector.controller',
    './language-handler.factory',
    './language-data.factory'
], function (
    diLanguageDirectiveLoader,
    languageSelectorControllerLoader,
    languageHandlerLoader,
    languageDataLoader) {
    'use strict';

    return function (module) {
        diLanguageDirectiveLoader(module);
        languageSelectorControllerLoader(module);
        languageHandlerLoader(module);
        languageDataLoader(module);
    };
});


