// di.core.angular.exception
define([
    'stacktrace'
], function (stackTrace) {
    'use strict';

    return function (module) {
        module.factory('stackTrace', stackTraceFactory);

        /**
         * Produces better exception stack traces
         */
        function stackTraceFactory() {
            return ({
                print: stackTrace
            });
        }

        return stackTraceFactory;
    };
});


