define([
    './_components/_loader',
    './purchaseOrderViewerLinesConfigurator',
	'./purchaseOrderViewerController'
], function (
	componentsLoader,
    purchaseOrderViewerLinesConfiguratorLoader,
	purchaseOrderViewerControllerLoader) {
    'use strict';

    return function (module) {
        componentsLoader(module);
        purchaseOrderViewerLinesConfiguratorLoader(module);
        purchaseOrderViewerControllerLoader(module);
    };
});



