define([
    './column-visibility-toggle.controller',
    './column-visibility-toggle.directive'
], function (
    columnVisibilityToggleControllerLoader,
    columnVisibilityToggleDirectiveLoader
	) {
	'use strict';

	return function (module) {
	    columnVisibilityToggleControllerLoader(module);
	    columnVisibilityToggleDirectiveLoader(module);
	};
});



