define([
    './product-lines.directive',
    './product-lines.controller',
    './column-visibility-toggle/_loader',
    './item-listener.controller'
], function (
    diProductLinesDirectiveLoader,
    productLinesControllerLoader,
    columnVisibilityToggleLoader,
    itemListenerControllerLoader) {
    'use strict';

    return function (module) {
        diProductLinesDirectiveLoader(module);
        productLinesControllerLoader(module);
        columnVisibilityToggleLoader(module);
        itemListenerControllerLoader(module);
    };
});


