define([
	'./related-documents.controller',
	'./related-documents.directive',
	'./related-documents-configurator.provider'
], function(
	relatedDocumentsControllerLoader,
	relatedDocumentsDirectiveLoader,
	relatedDocumentsConfiguratorLoader) {
	'use strict';

	return function (module) {
		relatedDocumentsControllerLoader(module);
		relatedDocumentsDirectiveLoader(module);
		relatedDocumentsConfiguratorLoader(module);
	};
});


