define([
	'./singleValueSummaryController',
	'./singleValueSummaryDirective'
], function (
	singleValueSummaryControllerLoader,
	singleValueSummaryDirectiveLoader) {
	'use strict';

	return function (module) {
		singleValueSummaryControllerLoader(module);
		singleValueSummaryDirectiveLoader(module);
	};
});



