define([
    './tax-code-selector.directive',
    './tax-code-selector.controller'
], function (
    diTaxCodeSelectorDirectiveLoader,
    taxCodeSelectorControllerLoader) {
    'use strict';

    return function (module) {
        diTaxCodeSelectorDirectiveLoader(module);
        taxCodeSelectorControllerLoader(module);
    };
});


