define(function () {
    'use strict';

    return function (module) {
        var guard = $di.exception.guard;
        var util = $di.utility.util;

        module.provider('activityMonitor', activityMonitor);

        function activityMonitor() {
           

            var provider = {
                configure: configure,
                $get: $get
            }

            provider.$get.$inject = [
                'userIdleActivityMonitor',
                'authenticationData',
                'authenticationClientEventSubscriber',
                'authenticationTicketValidator',
                'promptWindow',
                'translator',
                'LOGGED_OUT_REASON'
            ];

            return provider;

            function configure(cfg) {
              
            }

            function $get(
                userIdleActivityMonitor,
                authenticationData,
                authenticationClientEventSubscriber,
                authenticationTicketValidator,
                promptWindow,
                translator,
                LOGGED_OUT_REASON) {


                function sessionAboutToExpiry(idle, timeout) {
                    translator.translate('CORE.AUTHENTICATION.SESSION_EXPIRY_TITLE')
                        .then(function(translated) {
                            promptWindow.alert({
                                title: translated,
                                message: translator.instantTranslate('CORE.AUTHENTICATION.SESSION_EXPIRY',
                                    { idle: idle / 60, timeout: timeout / 60 }),
                                callback: function() {
                                    if (authenticationData.userData.isAuthenticated) {
                                        start();
                                        authenticationTicketValidator.validateTicket(true, function() {});
                                    }
                                }
                            });
                        });
                }

                function sessionTimeout() {
                    authenticationClientEventSubscriber.loggedOut(LOGGED_OUT_REASON.SessionTimeout);
                }

                return {
                    start: start,
                    stop: stop
                };

                function start() {
                    userIdleActivityMonitor.start({
                        idleStartedHandler: sessionAboutToExpiry,
                        idleEndHandler: function () { },
                        idleTimeoutHandler: sessionTimeout
                    });
                }

                function stop() {
                    userIdleActivityMonitor.stop();
                }
            }
        }

        return activityMonitor;
    };
});


