import {
    ResourceActionColumns,
    ResourceListConfigurationServiceBase,
    ResourceListColumns,
    ResourceListSortOptions,
    ResourceListTableOptions
} from '../../shared/resource-list-configuration-base.service';

import { Injectable } from 'ng-metadata/core';

@Injectable()
export class PartyCodeConfigurationService extends ResourceListConfigurationServiceBase {
    private _sortOptions: Array<ResourceListSortOptions>;
    private _columns: Array<ResourceListColumns>;
    private _actionColumns: Array<ResourceActionColumns>;

    constructor() {
        super();

        this._sortOptions = [];
        this._columns = [];
        this._actionColumns = [];
    }

    getSortOptions(): Promise<Array<ResourceListSortOptions>> {
        return Promise.resolve(this._sortOptions);
    }

    getTableOptions(): Promise<ResourceListTableOptions> {
        let tableOptions: ResourceListTableOptions = super.tableOptions();
        tableOptions.columns = this._columns;
        tableOptions.actionColumns = this._actionColumns;

        return Promise.resolve(tableOptions);
    }
}


