//{CODE:CAN01}
// di.core.angular
define([
	'./authentication/_loader',
	'./caching/_loader',
	'./exception/_loader',
	'./http/_loader',
	'./logging/_loader',
	'./userSecurity/_loader',
	'./session/_loader',
	'./state/_loader',
	'./translation/_loader',
	'./utility/_loader',
	'./validation/_loader',
    './watchers/_loader',
	'./window/_loader',
	'./currency/_loader',
	'./controls/_loader',
	'./staticLookup/_loader',
	'./date/_loader',
    './file/_loader',
    './url/_loader'
], function (
	authenticationLoader,
	cachingLoader,
	exceptionLoader,
	httpLoader,
	loggingLoader,
	userSecurityLoader,
	sessionLoader,
	stateLoader,
	translationLoader,
	utilityLoader,
	validationLoader,
    watchersLoader,
    windowLoader,
	currencyLoader,
	controlsLoader,
	staticLookupLoader,
	dateLoader,
    fileLoader,
    urlLoader
) {
	'use strict';

	return function (module) {		
	    authenticationLoader(module);
	    cachingLoader(module);
	    exceptionLoader(module);
		httpLoader(module);
		loggingLoader(module);
		userSecurityLoader(module);
		sessionLoader(module);
		stateLoader(module);
		translationLoader(module);
		utilityLoader(module);
		validationLoader(module);
	    watchersLoader(module);
	    windowLoader(module);
		currencyLoader(module);
		controlsLoader(module);
		staticLookupLoader(module);
		dateLoader(module);
		fileLoader(module);
	    urlLoader(module);
	};
});


