define([
    './attachment-uploader.component',
    './attachment-configurator.provider'
], function (
    attachmentUploader,
    attachmentConfiguratorProviderLoader
    ) {
    'use strict';

    return function (module) {
        module.component(
           attachmentUploader.AttachmentUploaderComponent.selector,
           attachmentUploader.AttachmentUploaderComponent);
        
        attachmentConfiguratorProviderLoader(module);
    };
});


