// di.einvoicing.ui.portal.proxies
define(function () {
	'use strict';

	return function (module) {
	    var util = $di.utility.util;

	    module.factory('identifierFactory', identifierFactory);

	    identifierFactory.$inject = ['translator', 'modalWindow'];

	    function identifierFactory(translator, modalWindow) {
			return {
				create: create,
				edit: edit,
			    getDefaultOptions: getDefaultOptions
			};
            
			function create(scheme, schemeId, code, codeDescription) {
			    var description = "";

			    if (util.isNonEmptyString(scheme)) {
			        description = translator.
			            instantTranslate("EINVOICING.COMPONENTS.CLIENT_ENUMS.SCHEMES." + scheme);
			    }


			    var codeDesc = undefined;

			    if (util.isNonEmptyString(codeDescription)) {
			        codeDesc = '(' + codeDescription + ')';
			    }

			    var display = "";

			    if (util.isNonEmptyString(scheme)) {

			        display = translator.
			            instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.DISPLAY",
			                { scheme: description, code: code, codeDesc: codeDesc });
			    } else {
			        display = translator.
                       instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.DISPLAY_NO_SCHEME",
                           { code: code, codeDesc: codeDesc });
			    }

			    var displayValue = display;
			    var label = display;

			    if (util.isNonEmptyString(code) === false || util.isNullOrUndefined(code)) {
			        code = "",
                    description = "";
			        scheme = "";
			        displayValue = "";
			        label = translator.instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.NONE");
			    }

			    return {
			        identifier: scheme + code,
			        code: code,
			        codeDescription : codeDesc,
			        scheme: scheme,
			        schemeId: schemeId,
			        schemeDescription: description,
			        displayValue: displayValue,
			        label: label
			    }
			}

			function edit(
                partyCode,
                schemes,
                relatedParties,
                validationPropertyName,
                callback,
                buttonLabels, 
                validate,
                partyId,
                partyCodeType) {
				formatSchemesForClient(schemes);

				const options = {
					dataModel: {
					    id: partyCode.id,
					    partyId: partyId,
					    partyCodeType: partyCodeType,
					    code: partyCode.code,
					    identifier: partyCode.code,
					    scheme: {
					        name: partyCode.scheme,
					        id: partyCode.schemeId,
					        value: partyCode.scheme
					    },
					    schemeId: partyCode.schemeId,
					    relatedParty: partyCode.relatedPartyDetails,
					    relatedParties: relatedParties,
					    options: schemes,
					    arrayName: 'edit',
					    action: undefined,
					    validationPropertyName: validationPropertyName,
					    isEditable: true
					},
					validateOnSave : validate,
					dropdownName: 'scheme',
					controller: "ResourceController",
					templateUrl:
						'src/app/portal/invoices/editor/_components/invoiceDetails/identifierPopup.html',
					onDataChanged: () => { },
					title: translator.
						instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ADD_NEW_IDENTIFIER_TITLE")
				};

				var labels = {};

				if (util.isDefined(buttonLabels)) {
				    labels = buttonLabels;
				}

				options.save = labels.save || "EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ACTIONS.SAVE";
				options.cancel = labels.cancel || "EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ACTIONS.CANCEL";


				showPopup(options, callback);
			}

			function getDefaultOptions(
                schemeOptions,
                arrayName,
                validationPropertyname,
                allowBlank,
                action,
                buttonLabels,
                validate) {
			    formatSchemesForClient(schemeOptions);

			    if (allowBlank) {
			        schemeOptions.unshift({
			            code: "",
			            numericalCode: "",
			            sortOrder: 0,
			            isEnabled: true,
			            id: 0,
			            displayValue: "",
			            showLabel: true,
			            label: translator.instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.NONE")
			        });
			    }

			    const none = {
			        identifier: "",
			        code: "",
			        scheme: "",
			        schemeDescription: "",
			        displayValue: "",
			        showLabel: true,
			        label: translator.instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.NONE")
			    }

			    const addNew = _.extend(_.clone(none), { addNew: addNewItem });

			    addNew.identifier = "NEW";
			    addNew.label = translator.
                    instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ADD_NEW");

			    const array = [
                    none,
                    addNew
			    ];

			    return array;

			    function addNewItem(callback, partyId, partyCodeType) {
			        const options = {
			            dataModel: {
			                partyId: partyId,
			                partyCodeType: partyCodeType,
			                identifier: '',
			                scheme: schemeOptions[0],
			                options: schemeOptions,
			                arrayName: arrayName,
                            action : action,
                            validationPropertyName: validationPropertyname,
                            isEditable: true
			            },
			            validateOnSave : validate,
			            dropdownName: 'scheme',
			            controller: "ResourceController",
			            templateUrl:
                            'src/app/portal/invoices/editor/_components/invoiceDetails/identifierPopup.html',
			            onDataChanged: () => { },
			            title: translator.
                            instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ADD_NEW_IDENTIFIER_TITLE")
			        };

			        var labels = {};

			        if (util.isDefined(buttonLabels)) {
			            labels = buttonLabels;
			        }

			        options.save = labels.save || "EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ACTIONS.SET";
			        options.cancel = labels.cancel || "EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ACTIONS.CANCEL";


			        showPopup(options, callback);
			    }
			}
			
			function showPopup(options, callback) {
				modalWindow.show(options,
					(result) => {

						var display;

						if (util.isNonEmptyString(result.scheme.name)) {

							display = translator.
								instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.DISPLAY",
								{
									scheme: result.scheme.name,
									code: result.code,
									codeDesc: result.
										codeDescription
								});
						} else {
							display = translator.
								instantTranslate("EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.DISPLAY_NO_SCHEME",
									{ code: result.code, codeDesc: result.codeDescription });
						}

						const newItem = {
							id: result.id,
							identifier: result.scheme.value + result.code,
							code: result.code,
							codeDescription: result.codeDescription,
							scheme: result.scheme.value,
							schemeId: result.scheme.id,
							schemeDescription: result.scheme.name,
							relatedParty: result.relatedParty,
							displayValue: display,
							label: display
						};

						if (util.isFunction(result.action)) {
							result.action(newItem, result.arrayName);
						}
						
						callback(newItem);
					},
					() => { });
			}

			function formatSchemesForClient(schemes) {
				var schemeTranslationPrefix = "EINVOICING.COMPONENTS.CLIENT_ENUMS.SCHEMES.";

				_.each(schemes,
					(scheme) => {
						scheme.name = translator.
							instantTranslate(schemeTranslationPrefix + scheme.code);
						scheme.value = scheme.code;
					});
			}

		}

	    return identifierFactory;
	};
});


