define([
	'./dropdown-selector/_loader',
	'./header/_loader',
	'./image/_loader',
	'./navigation/_loader',
	'./style-constants/_loader',
	'./tooltip/_loader',
	'./user/_loader',
	'./client-enums/_loader',
	'./service-status-monitor/_loader',
	'./identifier/_loader'
], function (
	dropdownSelectorLoader,
	headerLoader,
	imageLoader,
	navigationLoader,
	styleConstantsLoader,
	tooltipLoader,
	userLoader,
	clientEnumsLoader,
	serviceStatusMonitorLoader,
	identifierLoader) {
	'use strict';

	return function (module) {
		dropdownSelectorLoader(module);
		headerLoader(module);
		imageLoader(module);
		navigationLoader(module);
		styleConstantsLoader(module);
		tooltipLoader(module);
		userLoader(module);
		clientEnumsLoader(module);
		serviceStatusMonitorLoader(module);
		identifierLoader(module);
	};
});


