import { Injectable, Inject } from 'ng-metadata/core';

import { ResourceHandlerServiceBase } from '../../shared/resource-handler-base.service';

@Injectable()
export class LocationHandlerService extends ResourceHandlerServiceBase {
    public type: string = "location";

    constructor(
        @Inject('locationProxy') private locationProxy,
        @Inject('locationListConfigurationService') private locationListConfigurationService
    ) {
        super(locationListConfigurationService);
    }

    getResourceBook(partyId, forPartyId, sortBy = 1) {
        return this.locationProxy.getAddressBook(partyId, forPartyId, (sortBy || {}) || sortBy);
    }

    addNewResource(resource, forPartyId = null) {
        return this.locationProxy.addAddressBookEntry(resource, forPartyId);
    }

    updateResource(resource, resourceId) {
        return this.locationProxy.updateAddressBookEntry(resource, resourceId);
    }

    removeResource(resource, resourceId) {
        return this.locationProxy.removeAddressBookEntry(resource, resourceId);
    }
}

LocationHandlerService.prototype.constructor.$inject = ['locationProxy', 'locationListConfigurationService'];


