export const integratorDetailsComponent = {
    selector: 'diIntegratorDetails',
    controller: class {
        constructor(integratorDetailsProxy, urlHelper, $window, config) {
            this._integratorDetailsProxy = integratorDetailsProxy;
            this._urlHelper = urlHelper;

            this.clickonceConfig = {};
            this.installUrl = '';
            this.integratorSetupGuideUrl = config.app.integratorSetupGuideUrl; 
            this.download = () => {
                $window.open(this.installUrl);
            };
        }

        $onInit() {
            this._integratorDetailsProxy.getClickonceConfig()
				.then(clickonceConfig => {
				    this.clickonceConfig = clickonceConfig;
				    this.installUrl = this._urlHelper.combine(clickonceConfig.clickonceInstallUrl, clickonceConfig.clickonceFilename);
				});
        }
    },
    templateUrl: 'src/app/portal/utilities/integrator/integrator-details.html'
}

integratorDetailsComponent.controller.prototype.constructor.$inject = ['integratorDetailsProxy', 'urlHelper', '$window', 'PORTAL_CONFIG'];


