define([
	'./_snippets/_loader',
	'./purchaseOrderDetails/purchaseOrderDetailsDirective',
	'./dateRangeDisplay/_loader'
], function (
	snippetsLoader,
	purchaseOrderDetailsDirectiveLoader,
	dateRangeDisplayLoader) {
	'use strict';

	return function (module) {
		snippetsLoader(module);
		purchaseOrderDetailsDirectiveLoader(module);
		dateRangeDisplayLoader(module);
	};
});


