// di.einvoicing.ui.portal.portal.summaries.components
define([
	'./_snippets/_loader',
	'./summarySelectorController',
	'./summaryTypes',
	'./rangeSelector/_loader',
	'./tabularSummary/_loader',
	'./singleValueSummary/_loader',
	'./chartSummary/_loader'
], function (
	snippetsLoader,
	summarySelectorControllerLoader,
	summaryTypesLoader,
	rangeSelectorLoader,
	tabularSummaryLoader,
	singleValueSummaryLoader,
    chartSummaryLoader) {
    'use strict';

    return function (module) {
        snippetsLoader(module);
        summarySelectorControllerLoader(module);
        summaryTypesLoader(module);
        rangeSelectorLoader(module);
        tabularSummaryLoader(module);
        singleValueSummaryLoader(module);
        chartSummaryLoader(module);
    };
});



