declare var $di: any;
declare var _: any;

let util = $di.utility.util;

import { Component, Inject, OnInit } from 'ng-metadata/core';
import { LocationHandlerService } from './locations/shared';
import { ContactHandlerService } from './contacts/shared';
import { PartyCodeHandlerService } from './party-codes/shared/party-code-handler.service';

@Component({
    selector: 'diParty',
    templateUrl: 'src/app/portal/parties/party.component.html',
    providers: [
        { provide: 'contactHandlerService', useClass: ContactHandlerService },
        { provide: 'locationHandlerService', useClass: LocationHandlerService },
        { provide: 'partyCodeHandlerService', useClass: PartyCodeHandlerService }
    ]
})
export class PartyComponent implements OnInit {
    public forPartyId;
    public forPartyName;
    public partyId;
    public parties;
    public partyName;
    public readonly;
    public singleDetails;

    public locations;
    public locationSortOptions;
    public locationTableOptions;
    public locationListObject;

    public contacts;
    public contactSortOptions;
    public contactTableOptions;
    public contactListObject;
    
    public partyCodes;
    public partyCodeSortOptions;
    public partyCodeTableOptions;
    public partyCodeListObject;

    public selectedLocationSortOption;
    public selectedContactSortOption;
    constructor(
        @Inject('locationHandlerService') private locationHandlerService: LocationHandlerService,
        @Inject('contactHandlerService') private contactHandlerService: ContactHandlerService,
        @Inject('partyCodeHandlerService') private partyCodeHandlerService: PartyCodeHandlerService,
        @Inject('$stateParams') private stateParams,
        @Inject('$q') private $q,
        @Inject('notificationHandler') private notificationHandler,
        @Inject('userRightsRepository') private userRightsRepository,
        @Inject('diState') private diState,
        @Inject('partyProxy') private partyProxy,
        @Inject('listsParamsStore') private listsParamsStore
    ) {
        this.displayErrorNotification = this.displayErrorNotification.bind(this);
    }

    ngOnInit() {       
        this.partyId = this.stateParams.id;
        this.partyName = this.stateParams.partyName;
        this.trySetPartyNameIfNotSetAsync(this.partyId, this.partyName)
        this.forPartyId = this.stateParams.forPartyId;
        this.singleDetails = this.stateParams.singleDetails;
        this.forPartyName = this.stateParams.forPartyName;
        this.trySetPartyNameIfNotSetAsync(this.forPartyId, this.forPartyName);

        const locationPromises = this.initialise(this.locationHandlerService);

        this.$q.all(locationPromises).then(([items, tableOptions, sortOptions, parties]) => {
                this.locations = items;
                this.locationSortOptions = sortOptions;
                this.locationTableOptions = tableOptions;
                this.parties = parties;
                this.locationListObject = {
                    index: 1,
                    items,
                    parties
                };

                this.selectedLocationSortOption = this.getSelectedOrDefault(sortOptions, this.locationHandlerService);
            },
            this.displayErrorNotification);

        const contactPromises = this.initialise(this.contactHandlerService);

        this.$q.all(contactPromises).then(([items, tableOptions, sortOptions, parties]) => {
                this.contacts = items;
                this.contactSortOptions = sortOptions;
                this.contactTableOptions = tableOptions;
                this.parties = parties;
                this.contactListObject = {
                    index: 1,
                    items,
                    parties
                };

                this.selectedContactSortOption = this.getSelectedOrDefault(sortOptions, this.contactHandlerService);
            },
            this.displayErrorNotification);

        const partyCodePromises = this.initialise(this.partyCodeHandlerService);

        this.$q.all(partyCodePromises).then(([items, tableOptions, sortOptions, parties]) => {
            this.partyCodes = items;
            this.partyCodeSortOptions = sortOptions;
            this.partyCodeTableOptions = tableOptions;
            this.parties = parties;
            this.partyCodeListObject = {
                index: 1,
                items,
                parties
            };
        },
            this.displayErrorNotification);

        this.readonly = !this.userRightsRepository.userHasRight("Supervisor");
    }

    private initialise(handlerService) {
        const savedSortOption = this.stateParams[handlerService.type + 'SortOption'];
        let sortOption = 1;
        if (savedSortOption) {
            sortOption = savedSortOption;            
        }

        let promises = handlerService.init(this.partyId, this.forPartyId, sortOption);

        let partiesPromise = this.partyProxy.getPartyWithAccountingCustomers(this.partyId);

        promises.push(partiesPromise);

        return promises;
    }

    private getSelectedOrDefault(sortCollection,handlerService):any{
        return _.findWhere(sortCollection,
            { value: this.getSortOption(handlerService) });
    }
    private getSortOption(handlerService):number {
        const savedSortOption = this.stateParams[handlerService.type + 'SortOption'];
        let sortOption:number = 1;
        if (savedSortOption) {
            sortOption = savedSortOption;
        }
        return sortOption;
    }

    private addNew(resource, handlerService) {
        let promise = handlerService.addNewResource(resource, this.forPartyId);

        this.$q.when(promise).then((data) => {
                if (!data) {
                    this.displayErrorNotification({ failureReason: data.message });
                } else {
                    this.refreshData(1, handlerService);
                    this.displaySuccessNotification('EINVOICING.PORTAL.PARTIES.ACTIONS.ADDED', handlerService.type);
                }
            },
            this.displayErrorNotification);
    }

    private update(resource, handlerService) {
        let promise = handlerService.updateResource(resource, resource.id);
        this.$q.when(promise).then((data) => {
                if (!data) {
                    this.refreshData(1, handlerService);
                    this.displayErrorNotification({ failureReason: data.message });
                } else {
                    this.refreshData(1, handlerService);
                    this.displaySuccessNotification('EINVOICING.PORTAL.PARTIES.ACTIONS.UPDATED',
                        this.titleCase(handlerService.type));
                }
            },
            this.displayErrorNotification);
    }

    private delete(resource, handlerService) {
        console.log(resource, "delete");
        //let promise = handlerService.updateResource(resource, resource.id);
        //this.$q.when(promise).then((data) => {
        //        if (!data) {
        //            this.refreshData(1, handlerService);
        //            this.displayErrorNotification({ failureReason: data.message });
        //        } else {
        //            this.refreshData(1, handlerService);
        //            this.displaySuccessNotification('EINVOICING.PORTAL.PARTIES.ACTIONS.UPDATED',
        //                this.titleCase(handlerService.type));
        //        }
        //    },
        //    this.displayErrorNotification);
    }

    private remove(resource, handlerService) {
        let promise = handlerService.removeResource(resource, resource.id);
        this.$q.when(promise).then(() => {
                this.refreshData(1, handlerService);
                this.displaySuccessNotification('EINVOICING.PORTAL.PARTIES.ACTIONS.REMOVED',
                    this.titleCase(handlerService.type));
            },
            this.displayErrorNotification);
    }

    titleCase(str) {
        str = str.toLowerCase().split(' ').map((word) => {
            return (word.charAt(0).toUpperCase() + word.slice(1));
        });
        return str.join(' ');
    }

    private refreshData(sortBy, handlerService) {
        let promise = handlerService.getResourceBook(this.partyId, this.forPartyId, sortBy);
        this.$q.when(promise).then(items => {
                if (handlerService.type === 'location') {
                    this.locations = items;
                    let cloned = _.clone(this.locationListObject);
                    cloned.items = items;
                    this.locationListObject = cloned;
                } else if (handlerService.type === 'contact') {
                    this.contacts = items;
                    let cloned = _.clone(this.contactListObject);
                    cloned.items = items;
                    this.contactListObject = cloned;
                }
            },
            this.displayErrorNotification);
    }

    private sortByChanged(value, handlerService) {
        let newParams = this.listsParamsStore.get("partyDetails");

        if (!newParams) {
            newParams = [];
        }

        newParams[handlerService.type + 'SortOption'] = value;

        const params = _.extend({}, newParams);

        this.listsParamsStore.set("partyDetails", params);

        const redirectParams = _.extend({
                id: this.partyId,
                partyName: this.partyName,
                forPartyId: this.forPartyId,
                forPartyName: this.forPartyName

            },
            params);

        this.diState.go(this.stateParams.ownStateName, redirectParams, { reload: true });
    }

    private displayErrorNotification(reason: any = {}) {
        this.notificationHandler.showError(reason.failureReason);
    }

    private displaySuccessNotification(message, identifier) {
        this.notificationHandler.showSuccess(message, identifier);
    }

    backToList() {
        if (this.partyId === this.forPartyId) {
            this.diState.go('portal.parties.mylist');
        } else {
            let params = this.listsParamsStore.get("parties");
            this.diState.go('portal.parties.list', params);
        }
    }

    trySetPartyNameIfNotSetAsync(partyIdPropertyName, partyNamePropertyName) {
        if (util.isNumber(partyIdPropertyName) && partyIdPropertyName > 0 &&
            !util.isNonEmptyString(partyNamePropertyName)) {
            this.partyProxy.get(partyIdPropertyName)
                .then(party => {
                        partyNamePropertyName = this.stateParams[partyNamePropertyName] = party.name;
                    },
                    error => {});
        }
    }

    locationSortByChanged(value) {        
        this.sortByChanged(value, this.locationHandlerService);       
    }

    locationAddNew(resource) {
        this.addNew(resource, this.locationHandlerService);
    }

    locationUpdate(resource) {
        this.update(resource, this.locationHandlerService);
    }

    locationRemove(resource) {
        this.remove(resource, this.locationHandlerService);
    }

    locationReloadData() {
        this.refreshData(1, this.locationHandlerService);
    }

    contactSortByChanged(value) {
        this.sortByChanged(value, this.contactHandlerService);
    }

    contactAddNew(resource) {
        this.addNew(resource, this.contactHandlerService);
    }

    contactUpdate(resource) {
        this.update(resource, this.contactHandlerService);
    }

    contactRemove(resource) {
        this.remove(resource, this.contactHandlerService);
    }

    contactReloadData() {
        this.refreshData(1, this.contactHandlerService);
    }

    partyCodeSortByChanged(value) {
        this.sortByChanged(value, this.partyCodeHandlerService);
    }

    partyCodeAddNew(resource) {
        this.addNew(resource, this.partyCodeHandlerService);
    }

    partyCodeUpdate(resource) {
        this.update(resource, this.partyCodeHandlerService);
    }

    partyCodeDelete(resource) {
        this.delete(resource, this.partyCodeHandlerService);
    }

    partyCodeRemove(resource) {
        this.remove(resource, this.partyCodeHandlerService);
    }

    partyCodeReloadData() {
        this.refreshData(1, this.partyCodeHandlerService);
    }
}


