// di.einvoicing.ui.portal.portal.utilities.integrator
define([
    './integrator-details-proxy.service',
	'./integrator-details.component'
], function (
	integratorDetailsProxyServiceModule,
	integratorDetailsComponentModule) {
	'use strict';

	return function (module) {
	    module.service('integratorDetailsProxy', integratorDetailsProxyServiceModule.IntegratorDetailsProxyService);

	    module.component(
            integratorDetailsComponentModule.integratorDetailsComponent.selector,
            integratorDetailsComponentModule.integratorDetailsComponent);
	};
});



