// di.core.angular.controls.image
define([
    './diImageTemplate',
    './diImageCompile',
    './diImageDirective'
], function (
    imageTemplate,
	imageCompile,
    imageDirectiveLoader
    ) {
    'use strict';

    return function (module) {
        imageTemplate(module);
        imageCompile(module);
        imageDirectiveLoader(module);
    };
});


