// di.einvoicing.ui.portal.components
define(function () {
    'use strict';

    return function (module) {
        module.directive('diTooltip', diTooltip);

        function diTooltip() {
            return {
                restrict: 'A',
                link: function (scope, element, attrs) {
                    element.hover(function () {
                        element.tooltip('show');
                    }, function () {
                        element.tooltip('hide');
                    });
                }
            };
        }

        return diTooltip;
    };
});


