define([
	'./_states',
	'./shared/_loader',
    '../shared/services/_loader',
	'./invoices/_loader',
	'./account/_loader',
	'./purchaseOrders/_loader',
	'./users/_loader',
	'./audit/_loader',
	'./summaries/_loader',
	'./related/_loader',
	'./grn/_loader',
	'./portalController',
	'./forceUrlReloader',
	'./utilities/_loader',
	'./parties/_loader'
], function (
	statesLoader,
	sharedLoader,
    sharedServicesLoader,
	invoicesLoader,
	accountLoader,
	purchaseOrdersLoader,
	usersLoader,
	auditLoader,
	summariesLoader,
	relatedLoader,
	grnLoader,
	portalController,
	forceUrlReloaderLoader,
	utilitiesLoader,
	partiesLoader) {
	'use strict';

	return function (module) {
		statesLoader(module);
		sharedLoader(module);
		sharedServicesLoader(module);
		invoicesLoader(module);
		accountLoader(module);
		purchaseOrdersLoader(module);
		usersLoader(module);
		auditLoader(module);
		summariesLoader(module);
		relatedLoader(module);
		grnLoader(module);
		portalController(module);
		forceUrlReloaderLoader(module);
		utilitiesLoader(module);
		partiesLoader(module);
	};
});



