// di.einvoicing.ui.portal.portal.invoices.lists
define([
    './_components/_loader',
	'./purchaseOrderListFiltersController',
	'./purchaseOrderListController',
    './purchaseOrderListConfigurator'
], function (
	componentsLoader,
	purchaseOrderListFiltersControllerLoader,
	purchaseOrderListControllerLoader,
    purchaseOrderListConfiguratorLoader) {
	'use strict';

	return function (module) {
		componentsLoader(module);
		purchaseOrderListFiltersControllerLoader(module);
		purchaseOrderListControllerLoader(module);
		purchaseOrderListConfiguratorLoader(module);
	};
});



