//{CODE:CAC05}
// di.core.angular.controls.elementNotifierStyles
define([
    'jquery'
], function ($) {
	'use strict';

	return function () {
		$.notify.addStyle('diSuccess', {
			html: '<div>' +
				'<span class="fa fa-check fa-lg fa-pull-left">' +
				'</span>' +
				'<span class="padded-container" data-notify-text/>' +
				'</div>',
			classes: {
				base: {
					"font-weight": "bold",
					"padding": "8px 0px 8px 14px",
					"text-shadow": "0 1px 0 rgba(255, 255, 255, 0.5)",
					"border": "1px solid #fbeed5",
					"border-radius": "4px",
					"white-space": "nowrap",
				},
				success: {
					"color": "#468847",
					"background-color": "#DFF0D8",
					"border-color": "#D6E9C6",
				}
			}
		});

		$.notify.addStyle('diWarning', {
			html: '<div>' +
				'<span class="fa fa-warning fa-lg fa-pull-left">' +
				'</span>' +
				'<span class="padded-container" data-notify-text/>' +
				'</div>',
			classes: {
				base: {
					"font-weight": "bold",
					"padding": "8px 0px 8px 14px",
					"text-shadow": "0 1px 0 rgba(255, 255, 255, 0.5)",
					"border": "1px solid #fbeed5",
					"border-radius": "4px",
					"white-space": "nowrap",
				},
				warn: {
					"color": "#C09853",
					"background-color": "#FCF8E3",
					"border-color": "#FBEED5"
				}
			}
		});

		$.notify.addStyle('diInfo', {
			html: '<div> ' +
				'<span class="fa fa-info-circle fa-lg fa-pull-left">' +
				'</span>' +
				'<span class="padded-container" data-notify-text/>' +
				'</div>',
			classes: {
				base: {
					"font-weight": "bold",
					"padding": "8px 0px 8px 14px",
					"text-shadow": "0 1px 0 rgba(255, 255, 255, 0.5)",
					"border": "1px solid #fbeed5",
					"border-radius": "4px",
					"white-space": "nowrap",
				},
				info: {
					"color": "#3A87AD",
					"background-color": "#D9EDF7",
					"border-color": "#BCE8F1"
				}
			}
		});

		$.notify.addStyle('diError', {
			html: '<div>' +
				'<span class="fa fa-times fa-lg fa-pull-left">' +
				'</span>' +
				'<span class="padded-container" data-notify-text/>' +
				'</div>',
			classes: {
				base: {
					"font-weight": "bold",
					"padding": "8px 0px 8px 14px",
					"text-shadow": "0 1px 0 rgba(255, 255, 255, 0.5)",
					"border": "1px solid #fbeed5",
					"border-radius": "4px",
					"white-space": "nowrap",
				},
				error: {
					"color": "#B94A48",
					"background-color": "#F2DEDE",
					"border-color": "#EED3D7"
				}
			}
		});
		
	};
});


