// di.core.angular.controls.datatable
define([
    './diDataTableDirective',
    './diDataTableController',
    './diTemplateCompilerDirective'
], function (
    diDataTableDirectiveLoader,
    diDataTableControllerLoader,
    diTemplateCompilerLoader) {
    'use strict';

    return function (module) {
        diTemplateCompilerLoader(module);
        diDataTableDirectiveLoader(module);
        diDataTableControllerLoader(module);
    };
});


