declare var $di: any;
var util:any = $di.utility.util;
declare var _: any;

import { Component, Input, Output, EventEmitter, Inject } from 'ng-metadata/core';

@Component({
    selector: 'diPartyCode',
    templateUrl: 'src/app/portal/parties/party-codes/party-code.component.html'
})
    export class PartyCodeComponent{
        @Input() readonly:any;
        @Input() partyCodes:any;
        @Input() relatedParties:any;
        @Input() partyCodeType:any;
        @Input() partyId: any;

        @Output() addNewCode:EventEmitter<any> = new EventEmitter<any>();
        @Output() updatePartyCode: EventEmitter<any> = new EventEmitter<any>();
        @Output() deletePartyCode: EventEmitter<any> = new EventEmitter<any>();

        public partyCodeTypeSchemes: any;
        public partyCodeOptions:any;
        constructor(
            @Inject('partyProxy') private partyProxy,
            @Inject('identifierFactory') private identifierFactory,
            @Inject('CLIENT_ENUMS') private clientEnums,
            @Inject('modalWindow') private modalWindow,
            @Inject('elementNotifier') private elementNotifier,
            @Inject('$element') private $element,
            @Inject('$q') private $q)
        {
            this.partyProxy = partyProxy;        
            this.identifierFactory = identifierFactory;
            this.clientEnums = clientEnums;
            this.modalWindow = modalWindow;
            this.elementNotifier = elementNotifier;
            this.$element = $element;
            this.$q = $q;
        }

        $onInit() {
            this.partyProxy.getEnabledSchemes().then((schemes) => {
                this.partyCodeTypeSchemes = schemes[this.clientEnums.partyCodeType[this.partyCodeType]];

                this.partyCodeOptions = this.identifierFactory.getDefaultOptions(
                    schemes[this.clientEnums.partyCodeType[this.partyCodeType]],
                    'partyCodes',
                    'identifier',
                    false,
                    null,
                    this.getButtonLabels(),
                    true);
            });
        }
        
        public editPartyCode(partyCode) {
            this.identifierFactory.edit(partyCode, this.partyCodeTypeSchemes, this.relatedParties, 'identifier', (data) => {
                data.partyCodeType = this.clientEnums.partyCodeType[this.partyCodeType];
                this.partyCodes = _.map(this.partyCodes, (partyCode) => {
                    if(partyCode.id === data.id){
                        partyCode.id = data.id;
                        partyCode.code = data.code;
                        partyCode.scheme = data.scheme;
                        partyCode.schemeId = data.schemeId;
                        
                        if(util.isDefined(data.relatedParty)){
                            partyCode.relatedPartyDetails = data.relatedParty;
                            partyCode.relatedParty = data.relatedParty.id;
                            data.relatedPartyId = data.relatedParty.id;                            
                        }
                    }
    
                    return partyCode;
                });

                this.updatePartyCode.emit({data});
            },
            this.getButtonLabels(),
            true, 
            this.partyId, 
            this.partyCodeType);
        }

        public addNewPartyCode() {
                this.partyCodeOptions[1].addNew((data) => {
                    data.type = this.partyCodeType;
                    this.addNewCode.emit({ data });

                    this.partyCodes.push(data);
                }, 
                this.partyId, 
                this.partyCodeType);

            }

        public delete(partyCode: any): void {
            this.deletePartyCode.emit({partyCode});
        }

        getButtonLabels() {
            return {
                save: "EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ACTIONS.SAVE",
                cancel: "EINVOICING.PORTAL.COMPONENTS.IDENTIFIER_SELECTOR.ACTIONS.CANCEL"
            };
        }
}


