define([
	'./document-state-display.directive',
	'./document-state-display.controller'
], function (
	documentStateDisplayDirectiveLoader,
	documentStateDisplayControllerLoader) {
	'use strict';

	return function (module) {
		documentStateDisplayDirectiveLoader(module);
		documentStateDisplayControllerLoader(module);
	};
});


