define([
	'./unit-of-measure-selector.directive',
	'./unit-of-measure.controller',
	 './unit-of-measure-repository.factory'
], function (
	unitOfMeasureSelectorDirectiveLoader,
	unitOfMeasureControllerLoader,
	unitOfMeasureRepositoryLoader) {
	'use strict';

	return function (module) {
		unitOfMeasureSelectorDirectiveLoader(module);
		unitOfMeasureControllerLoader(module);
		unitOfMeasureRepositoryLoader(module);
	};
});


