define([
	'./reconciliation-failure-formatter.factory',
	'./business-validation-failure-formatter.factory',
	'./document-issues-message-builder.factory'
], function (
	reconciliationFailureFormatterLoader,
	businessValidationFailureFormatterLoader,
	documentIssuesMessageBuilderLoader) {
	'use strict';

	return function (module) {
	    reconciliationFailureFormatterLoader(module);
	    businessValidationFailureFormatterLoader(module);
	    documentIssuesMessageBuilderLoader(module);
	};
});


