// di.einvoicing.ui.portal.components.styleConstants
define(function () {
    'use strict';

    return function (module) {
        module.constant('COLOUR_CLASSES', colourClasses());

        function colourClasses() {
            return {
                offWhite: "#E6E7EA",
                button: "#4F87E6",
                icon: "#ABB0B8",
                negativeButton: "#A31515"
            };
        }
    };
});


