export const SecurityQuestionComponent = {
    selector: 'diSecurityQuestion',
    controller: class {
        constructor(
            accountService,
            authenticationData, 
            userSecurityProxy, 
            modalSpinner, 
            notificationHandler
            ) {
            this.accountService = accountService;
            this.userSecurityProxy = userSecurityProxy;
            this.modalSpinner = modalSpinner;
            this.notificationHandler = notificationHandler;
            
            this.securityAnswer = '';
            this.securityQuestions = [];
            this.securityQuestion = '';
            this.securityAnswerTemplate = '';
            this.securityQuestionEditable = false;
            this.currentUsername = authenticationData.userData.username;

            this.editSecurityQuestion = this.editSecurityQuestion.bind(this);
            this.cancelSecurityQuestionEdit = this.cancelSecurityQuestionEdit.bind(this);
            this.setSecurityQuestionAndAnswer = this.setSecurityQuestionAndAnswer.bind(this);
        }

        $onInit() {
            if (this.accountService.currentUser.securityAnswerHash) {
                this.securityAnswerTemplate = '********';
            } else {
                this.securityAnswerTemplate = '';
            }

            this.getSecurityQuestions();
        }

        getSecurityQuestions() {
            this.showSpinner();

            let promise = this.userSecurityProxy.getSecurityQuestions();
            promise.then(data => {
                let questions = [];

                _.each(data, function(item, index) {
                    questions.push({
                        id: index,
                        question: item
                    });
                });
                
                this.securityQuestions.fill(questions);

                this.findSelectedQuestion();
					
                this.removeSpinner();
            }, () => {
                this.removeSpinner();
            });
        }

        showSpinner() {
            window.spinner = this.modalSpinner.showModalSpinner("#spinnerHolder");
        }

        removeSpinner() {
            if (window.spinner) {
                window.spinner.remove();
            }
        }

        findSelectedQuestion() {
            let currentQuestion = _.find(this.securityQuestions, (item) => {
                return item.question === this.accountService.currentUser.securityQuestion;
            });

            if (currentQuestion) {
                this.securityQuestion = currentQuestion;
            }
            else if (this.accountService.currentUser.securityQuestion) {
                this.securityQuestions.push({
                    id: this.securityQuestions.length,
                    question: this.accountService.currentUser.securityQuestion
                });
                this.findSelectedQuestion();
            }
        }
        
        editSecurityQuestion() {
            this.securityAnswer = '';
            this.securityQuestionEditable = true;
        }

        cancelSecurityQuestionEdit() {
            this.findSelectedQuestion();
            this.securityAnswer = '';

            this.securityQuestionEditable = false;
        }

        setSecurityQuestionAndAnswer() {
            let promise = this.userSecurityProxy.setSecurityQuestionAndAnswer(
                this.currentUsername,
                this.securityQuestion.question,
                this.securityAnswer);

            promise.then(() => {
                this.notificationHandler.showSuccess('EINVOICING.PORTAL.ACCOUNT.MANAGEMENT.SECURITY_QUESTION_SET');

                this.securityQuestionEditable = false;

                this.accountService.getUser(()=>{}, ()=>{});
            });
        }
    },
    templateUrl: 'src/app/portal/account/security-question/security-question.component.html'
}

SecurityQuestionComponent.controller.prototype.constructor.$inject = [
    'accountService',
    'authenticationData',
    'userSecurityProxy',
    'modalSpinner',
    'notificationHandler'
];


