define([
	'./_components/_loader',
	'./invoiceViewerController' ,
	'./invoiceViewDetailsButtonsConfigurator'
   
], function (
	componentsLoader,
	invoiceViewerControllerLoader,
	invoiceViewDetailsButtonsConfiguratorLoader
	) {
	'use strict';

	return function (module) {
		componentsLoader(module);
		invoiceViewerControllerLoader(module);
		invoiceViewDetailsButtonsConfiguratorLoader(module);
	};
});



