define([
	'./_components/_loader',
	'./buyerSummary/_loader',
	'./supplierSummary/_loader',
	'./clickThroughDataMapper',
    './summary-proxy.factory'
], function (
	componentsLoader,
	buyerSummaryLoader,
	supplierSummaryLoader,
	clickThroughDataMapper,
    summaryProxyLoader
	) {
	'use strict';

	return function (module) {
	    summaryProxyLoader(module);
		componentsLoader(module);
		buyerSummaryLoader(module);
		supplierSummaryLoader(module);
		clickThroughDataMapper(module);
	};
});



