define(['moment'], function (moment) {
	'use strict';

	return function (module) {
		module.constant('DATE_FORMATS', dateFormats());

		function dateFormats() {
		    var supportedDateFormats = [
                    "DD/MM/YYYY",
					moment.localeData().longDateFormat('L'),
					moment.localeData().longDateFormat('l'),
					moment.localeData().longDateFormat('LL'),
					moment.localeData().longDateFormat('ll'),
					moment.localeData().longDateFormat('LLL'),
					moment.localeData().longDateFormat('lll'),
					moment.localeData().longDateFormat('LLLL'),
					moment.localeData().longDateFormat('llll')
				]
				.concat([moment.ISO_8601, "YYYY/MM/DD"]);

			return {
				supported: supportedDateFormats
			};
		}

		return dateFormats;
	};
});


