define([
    './party-codes.component',
    './loader',
    './shared/_loader',
    
], function(
    partyCodesComponentLoader,
    partyCodeComponentLoader,
    sharedLoader) {
    'use strict';

    return function(module) {
        module.component(
            partyCodesComponentLoader.PartyCodesComponent.selector,
            partyCodesComponentLoader.PartyCodesComponent);
        partyCodeComponentLoader.load(module);
        sharedLoader.load(module);
    };
});


