export const AccountManagementComponent = {
    selector: 'diAccountManagement',
    controller: class {
        constructor(
            accountService,
            authenticationData, 
            modalSpinner, 
            notificationHandler, 
            diState
            ) {
            this.accountService = accountService;
            this.authenticationData = authenticationData;
            this.modalSpinner = modalSpinner;
            this.notificationHandler = notificationHandler;
            this.diState = diState;
            
            this.getUser = this.getUser.bind(this);
            this.subscribeToUsernameChange = this.subscribeToUsernameChange.bind(this);
        }

        $onInit() {
            this.currentUsername = this.authenticationData.userData.username;
            this.currentUser = this.accountService.currentUser;
            this.getUser();
        }

        getUser() {
            this.showSpinner();
            this.accountService.getUser(() => {
                    this.removeSpinner();
                },
                (error) => {
                    this.removeSpinner();
                    if (error) {
                        this.notificationHandler.showError(error);
                    } else {
                        this.notificationHandler.showError('EINVOICING.PORTAL.ACCOUNT.MANAGEMENT.COULD_NOT_LOAD_USER');
                    }
                    this.diState.goBack().catch(()=> {
                        this.diState.go('portal.dashboard');
                    });
                });
        }

        subscribeToUsernameChange(subscription) {
            subscription(this.currentUsername);
        }
        
        showSpinner() {
            window.spinner = this.modalSpinner.showModalSpinner("#spinnerHolder");
        }

        removeSpinner() {
            if (window.spinner) {
                window.spinner.remove();
            }
        }
    },
    templateUrl: 'src/app/portal/account/account-management/account-management.component.html'
}

AccountManagementComponent.controller.prototype.constructor.$inject = [
    'accountService',
    'authenticationData', 
    'modalSpinner',
    'notificationHandler',
    'diState'
];


