class PartyFeatureEnabledProxyService {
	constructor(webApiHttp, urlHelper) {
		this._webApiHttp = webApiHttp;
		this._urlHelper = urlHelper;
	}

	getPartyFeatureEnabled(featureId) {
		let requestedUrl = 'EInvoicingFeature/GetPartyHasEInvoicingFeature?einvoicingFeature=' + featureId;

		return this._webApiHttp.get(requestedUrl);
	}
}

PartyFeatureEnabledProxyService.prototype.constructor.$inject = ['webApiHttp', 'urlHelper'];

export { PartyFeatureEnabledProxyService }



