define([
	'./grn-view-configurator.provider',
    './grn-view.controller',
    './grn-details.directive'
], function(
	grnViewConfiguratorLoader,
    grnViewControllerLoader,
    grnDetailsDirectiveLoader) {
	'use strict';

	return function(module) {
	    grnViewConfiguratorLoader(module);
	    grnViewControllerLoader(module);
	    grnDetailsDirectiveLoader(module);
	};
});


