define([
	'./navigation.controller',
    './navigation.directive',
    './navigation-warning.directive',
	'./navigation-warning.controller',
    './go-back.directive'
], function (
	navigationControllerLoader,
	diNavigationDirectiveLoader,
    navigationWarningLoader,
	navigationWarningControllerLoader,
    goBackLoader) {
	'use strict';

	return function (module) {
		navigationControllerLoader(module);
		diNavigationDirectiveLoader(module);
		navigationWarningLoader(module);
		navigationWarningControllerLoader(module);
	    goBackLoader(module);
	};
});


