let util = $di.utility.util;

export const UserDetailsComponent = {
    templateUrl: 'src/app/portal/users/user-details/user-details.component.html',
    selector: 'diUserDetails',
    bindings: {

    },
    controllerAs: 'userDetailsCtrl',
    controller:
    class UserDetailsController {

        constructor(stateParams,
            userProxy,
            partyProxy,
            diState,
            notificationHandler,
            translator,
            enumTranslator,
            userDetailsConfigurationService,
            authorizedObjectsFilter,
            authenticationData,
            clientEnums,
            associativeArrayConvertor,
            userRightsRepository,
            userSecurityProxy,
            userActionsResolver,
            modalWindow,
            modalSpinner,
            clipboard,
            validationRulesSetter) {
            this.stateParams = stateParams;
            this.userProxy = userProxy;
            this.partyProxy = partyProxy;
            this.diState = diState;
            this.notificationHandler = notificationHandler;
            this.translator = translator;
            this.enumTranslator = enumTranslator;
            this.userDetailsConfigurationService = userDetailsConfigurationService;
            this.authorizedObjectsFilter = authorizedObjectsFilter;
            this.authenticationData = authenticationData;
            this.clientEnums = clientEnums;
            this.associativeArrayConvertor = associativeArrayConvertor;
            this.userRightsRepository = userRightsRepository;
            this.userSecurityProxy = userSecurityProxy;
            this.userActionsResolver = userActionsResolver;
            this.modalWindow = modalWindow;
            this.modalSpinner = modalSpinner;
            this.clipboard = clipboard;
            this.validationRulesSetter = validationRulesSetter;
        }

        $onInit() {
            this.isDirty = false;
            this.isReadOnly = false;
            this.isSupervisor = true;
            this.mode = 'edit';
            this.allocatedPartyName = { name: '' };
            this.unallocated = { value: false };
            this.user = {};
            this.selectedRoleId = 0;
            this.allRoles = [];
            this.allRights = [];
            this.userRights = [];
            this.roleRights = [];
            this.allWorkingFor = [];
            this.workingIdsArray = [];
            this.allAllocatedTo = [];
            this.labels = {};
            this.selectedStates = [];
            this.selectedAttributes = [];
            this.selectedActions = [];
            this.mainButtonsOptions = {};
            this.createButtonsOptions = {};
            this.letterRangeFilter = {
                fromLetter: 'A',
                toLetter: 'Z'
            }
            
            this.selectedRestriction = { name: 'restricted' };
            this.eventsSubscriptions = [];

            this.loadUserData = this.loadUserData.bind(this);
            this.loadUserResolved = this.loadUserResolved.bind(this);
            this.filterByNameAndAllocation = this.filterByNameAndAllocation.bind(this);

            if (util.isNumber(this.stateParams.id)) {
                this.mode = 'edit';
            } else {
                this.mode = 'new';
            }

            this.setLabels();

            this.setOptions();

            this.initialiseRoles();

            this.loadUserData();
        }

        setLabels() {
            this.authorizedObjectsFilter.requireRights(false);
            const data = [
                {
                    key: "titleEdit",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.EDIT.HEADER.TITLE"
                },
                {
                    key: "titleNew",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.NEW.HEADER.TITLE"
                },
                {
                    rights: ["Supplier"],
                    key: "allocatedTitle",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.COMPONENTS.DETAILS.HEADING_ALLOCATED_TO_SUPPLIER"
                },
                {
                    rights: ["Buyer"],
                    key: "allocatedTitle",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.COMPONENTS.DETAILS.HEADING_ALLOCATED_TO_BUYER"
                },
                {
                    rights: ["Supplier"],
                    key: "allocatedDesc",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.DESCRIPTIONS.ALLOCATED_TO_SUPPLIER"
                },
                {
                    rights: ["Buyer"],
                    key: "allocatedDesc",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.DESCRIPTIONS.ALLOCATED_TO_BUYER"
                },
                {
                    rights: ["Supplier"],
                    key: "unallocatedTitle",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.COMPONENTS.DETAILS.HEADING_UNALLOCATED_SUPPLIER"
                },
                {
                    rights: ["Buyer"],
                    key: "unallocatedTitle",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.COMPONENTS.DETAILS.HEADING_UNALLOCATED_BUYER"
                },
                {
                    rights: ["Supplier"],
                    key: "unallocatedDesc",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.DESCRIPTIONS.UNALLOCATED_SUPPLIER"
                },
                {
                    rights: ["Buyer"],
                    key: "unallocatedDesc",
                    label: "EINVOICING.PORTAL.USERS.EDITOR.DESCRIPTIONS.UNALLOCATED_BUYER"
                }
            ];
            this.labels = this.authorizedObjectsFilter.getAuthorizedObjectsAsAssociative(data, 'key', 'label');

            if (this.mode === 'edit') {
                this.labels.title = this.labels.titleEdit;
            } else {
                this.labels.title = this.labels.titleNew;
            }
        }

        setOptions() {
            this.mainButtonsOptions = this.userDetailsConfigurationService.getMainButtonsConfiguration();
            this.createButtonsOptions = this.userDetailsConfigurationService.getCreateButtonsConfiguration();
            this.bottomButtonsOptions = this.userDetailsConfigurationService.getMainButtonsConfiguration();

            this.validationRulesSetter.setValidationRules("User");

        }

        initialiseRoles() {
            let roles = this.userDetailsConfigurationService.getRolesConfiguration();

            this.allRoles.fill(roles.visibleRoles);

            this.translator.whenReady().then(() => {
                _.each(this.allRoles,
                    (role) => {
                        role.enabled = false;
                        role.description = this.getRoleDescription(role.id);
                        role.name = this.getRoleName(role.id);
                    });
            });


        }

        getRoleDescription(roleId) {
            let roleName = this.getCurrentUserRoleName();

            if (util.isNonEmptyString(roleName)) {
                roleName = `_${roleName}`;
            }

            return this.translator.
                instantTranslate(`EINVOICING.PORTAL.USERS.EDITOR.ROLES.DESCRIPTION${roleName}.${roleId}`);
        }

        getRoleName(roleId) {
            return this.translator.instantTranslate(`EINVOICING.PORTAL.USERS.EDITOR.ROLES.NAME.${roleId}`);
        }

        getCurrentUserRoleName() {
            const isSupplier = this.userRightsRepository.userHasRight("Supplier");
            const isBuyer = this.userRightsRepository.userHasRight("Buyer");

            let role = '';

            if (isSupplier) {
                role = "SUPPLIER";
            } else if (isBuyer) {
                role = "BUYER";
            }

            return role;
        }

        loadUserData() {
            if (util.isNumber(this.stateParams.id)) {
                if (this.stateParams.id > 0) {
                    this.userProxy.getDetailsModelById(this.stateParams.id).then(this.loadUserResolved,
                        () => {
                            this.notificationHandler.
                                showError("EINVOICING.PORTAL.USERS.EDITOR.LOAD_ERROR", this.stateParams.id);
                            this.backToList();
                        });
                } else {
                    this.notificationHandler.showError("EINVOICING.PORTAL.USERS.EDITOR.LOAD_ERROR",
                        this.stateParams.id);
                    this.backToList();
                }
            } else {
                this.user = {
                    actions: [this.clientEnums.userActions.createUser],
                    isEnabled: true
                };

                this.partyProxy.getAllPartiesUserCanWorkFor(0).then((data) => {
                    _.each(data,
                        (item) => {
                            item.isAssigned = false;
                        });

                    this.allWorkingFor = data;

                    this.resolveActions();
                });
            }
        }

        loadUserResolved(data) {
            this.user = data;
            let promptDeleteUserMessage = this.translator.instantTranslate('EINVOICING.PORTAL.USERS.DELETE.CONFIRMATION_MESSAGE', this.user.name);
            this.mainButtonsOptions.promptMessage = this.bottomButtonsOptions.promptMessage = { deleteUser: promptDeleteUserMessage };

            this.selectedRestriction = {
                name: data.editRestrictions ? data.editRestrictions.toLowerCase() : ''
            };

            this.getTrueFalseLabel(this.user.enabled).then((enabled) => {
                this.user.enabledLabel = enabled;
            });
            this.getTrueFalseLabel(this.user.locked).then((locked) => {
                this.user.lockedLabel = locked;
            });

            this.partyProxy.getAllPartiesUserCanWorkFor(this.user.id).then((data) => {
                this.allWorkingFor = data;

                this.setAllocated(true);
            },
                () => {
                    this.notificationHandler.
                        showError("EINVOICING.PORTAL.USERS.EDITOR.LOAD_ERROR", this.stateParams.id);

                    this.backToList();
                });
        }

        /**
             * Event handler for filter by fist letter changes
             */
        onSearchRangeChanged() {
            if (util.isNonEmptyString(this.letterRangeFilter.fromLetter) && 
                util.isNonEmptyString(this.letterRangeFilter.toLetter)) {
                this.setLetters(this.letterRangeFilter.fromLetter, this.letterRangeFilter.toLetter);
            } else {
                this.setLetters('A', 'Z');
            }
        }

        setLetters(from, to) {
            this.letterRangeFilter.fromLetter = from.toUpperCase();
            this.letterRangeFilter.toLetter = to.toUpperCase();
        }

        /**
         * Searching by assigned count for supervisor
         */
        filterParties() {
            const items = this.allAllocatedTo;
            if (items.length === 0) {
                return items;
            }

            let filtered;
            if (this.isSupervisor) {

                filtered = _.filter(items,
                    function (item) {
                        return item.assignedCount === 0;
                    });

            } else {
                filtered = items;
            }

            return this.filterByNameAndAllocation(filtered);
        }

        filterByNameAndAllocation(items) {
            let filtered;
            const unallocated = this.unallocated.value;
            const allocatedPartyName = this.allocatedPartyName;

            if (util.isNonEmptyString(allocatedPartyName.name)) {
                filtered = _.filter(items,
                    function (item) {
                        const matched = item.name.toLowerCase().indexOf(allocatedPartyName.name.toLowerCase()) > -1;

                        if (unallocated) {
                            return matched && item.isAssigned === false;
                        } else {
                            return matched;
                        }
                    });
            } else {
                filtered = _.filter(items,
                    function (item) {
                        if (unallocated) {
                            return item.isAssigned === false;
                        } else {
                            return true;
                        }
                    });
            }

            return this.filterByRange(filtered, this.letterRangeFilter.fromLetter, this.letterRangeFilter.toLetter);
        }

        filterByRange(items, fromLetter, toLetter) {
            fromLetter = fromLetter.toLowerCase();
            toLetter = toLetter.toLowerCase();
            let filtered = [];
            for (var i = 0; i < items.length; i++) {
                var item = items[i];

                var firstLetter = item.name.substring(0, 1).toLowerCase();

                if ((!fromLetter || firstLetter >= fromLetter) && (!toLetter || firstLetter <= toLetter)) {
                    filtered.push(item);
                }
            }
            return filtered;
        }

        /**
         * loads allocated parties list and resets those settings
         */
        setAllocated(resetActions) {
            var workingIdsArray = [];
            _.each(this.allWorkingFor,
                (company) => {

                    if (company.isAssigned) {
                        workingIdsArray.push(company.partyId);
                    }
                });

            this.partyProxy.getAllRelatedPartyCountries(this.stateParams.id, workingIdsArray).then((data) => {
                var relatedIds = [];
                _.each(data,
                    (company) => {
                        relatedIds.push(company.party.id);
                    });

                this.partyProxy.getAllPartiesUserCanBeAllocatedTo(this.stateParams.id, relatedIds).then((data) => {

                    this.allAllocatedTo = _.uniq(data,
                        function (item) {
                            return item.partyId;
                        });

                    this.setRoles();
                    this.setRights();

                    if (resetActions) {
                        this.setActions();
                    }
                });
            });
        }

        setAllocatedResettingActions() {
            this.setAllocated(true);
        }

        subscribeToEvents(subscription) {
            this.eventsSubscriptions.push(subscription);
        }

        notifyEventsSubscribers(notificationObject) {
            for (var i = 0; i < this.eventsSubscriptions.length; i++) {
                this.eventsSubscriptions[i](notificationObject);
            }
        }

        setRoles() {
            if (this.user.roles.length > 0) {
                this.selectedRoleId = this.user.roles[0].id;
            }
        }

        setRights() {
            this.userProxy.getUserRights(this.user.id).then((data) => {

                var found = _.find(data, (right) => {
                    return right.name === 'Supervisor';
                });

                this.userRights = data;
                this.isSupervisor = util.isDefined(found);

                if (this.isSupervisor) {
                    _.each(this.allAllocatedTo, (related) => {
                        related.assigned = false;
                        related.allocated = false;
                    });
                }

                this.setRoleRights(false);
            });
        }

        setRoleRights(roleChanged) {
            this.userProxy.getRoleRights().then((roleRights) => {
                this.roleRights = roleRights;

                if (roleChanged) {
                    this.userRights = this.roleRights.find(x => x.role.id === this.selectedRoleId).rights;
                }

                this.userProxy.getAllRights().then((rights) => {
                    this.allRights = rights;

                    this.notifyEventsSubscribers({
                        userRights: this.userRights,
                        roleRights: this.roleRights,
                        allRights: this.allRights
                    });
                });
            });
        }

        setActions() {
            this.removeAction(this.clientEnums.userActions.save);
            this.resolveActions();
        }

        /**
         * Handler for form changed event
         */
        onChanged() {
            this.isDirty = true;
            this.addAction(this.clientEnums.userActions.save);
        }

        onRoleChanged() {
            this.onChanged();
            this.userRights = [];
            this.setRoleRights(true);
        }

        /**
         * Handler for workning company selection change event
         */
        onWorkingChanged() {
            if (this.mode === 'edit') {
                this.setAllocated();
            }

            this.onChanged();
        }

        /**
         * Enables user account
         */
        enable() {
            this.user.enabled = true;

            this.getTrueFalseLabel(true).then((translated) => {
                this.user.enabledLabel = translated;
            });

            this.removeAction(this.clientEnums.userActions.enable);
            this.addAction(this.clientEnums.userActions.disable);

            this.onChanged();
        }

        /**
         * Disables user account

         */
        disable() {
            this.user.enabled = false;
            this.getTrueFalseLabel(false).then((translated) => {
                this.user.enabledLabel = translated;
            });

            this.removeAction(this.clientEnums.userActions.disable);
            this.addAction(this.clientEnums.userActions.enable);

            this.onChanged();
        }

        /**
         * Unlocks user account
         */
        unlock() {
            this.user.locked = false;

            this.getTrueFalseLabel(false).then((translated) => {
                this.user.lockedLabel = translated;

                this.removeAction(this.clientEnums.userActions.unlock);
                this.addAction(this.clientEnums.userActions.lock);

                this.onChanged();
            });
        }

        /**
        * Validates user before saving
        */
        validate() {
            if (this.selectedRoleId === 0) {
                this.notificationHandler.showWarning('EINVOICING.PORTAL.USERS.EDITOR.SAVED_NO_ROLE_SELECTED');
                return false;
            }

            const workingForSelected = _.find(this.allWorkingFor,
                (model) => {
                    return model.isAssigned === true;
                });

            if (!util.isDefined(workingForSelected)) {
                this.notificationHandler.showWarning('EINVOICING.PORTAL.USERS.EDITOR.SAVED_NO_WORKING_PARTIES');
                return false;
            }

            return true;
        }

        /**
         * Creates new user              
         */
        create() {
            let selectedRoles = _.filter(this.allRoles,
                (role) => {
                    return role.id === this.selectedRoleId;
                });

            var selectedWorkingFor = [];

            _.each(this.allWorkingFor,
                (model) => {
                    if (model.isAssigned === true) {
                        selectedWorkingFor.push(model.party);
                    }
                });

            this.user.roles = selectedRoles;
            this.user.workingParties = selectedWorkingFor;
            this.user.allocatedParties = [];
            this.user.enabled = true;

            if (this.creatingUser !== true) {
                this.showSpinner();
                this.creatingUser = true;
                this.userProxy.createUser(this.user).then((data) => {
                    if (data.success) {
                        this.showPassword(data.generatedPassword, true);
                        this.diState.go('portal.users.editById', { id: data.userId });
                    } else {
                        this.notificationHandler.showError("EINVOICING.PORTAL.USERS.EDITOR.ERRORS." + data.failureReason.toUpperCase());
                    }
                },
                    () => {
                        this.notificationHandler.showError("EINVOICING.PORTAL.USERS.EDITOR.CREATE_ERROR");
                    }).finally(() => {
                        this.creatingUser = false;
                        this.removeSpinner();
                    });
            }
        }

        generatePassword() {
            if (this.generatingPassword !== true) {
                this.showSpinner();
                this.generatingPassword = true;
                this.userProxy.generatePassword(this.user.id).then((data) => {
                    this.showPassword(data.generatedPassword, false);
                },
                    () => {
                        this.notificationHandler.showError("EINVOICING.PORTAL.USERS.EDITOR.GENERATE_ERROR");
                    }).finally(() => {
                        this.generatingPassword = false;
                        this.removeSpinner();
                    });
            }
        }

        showPassword(password, created) {
            let options = {
                dataModel: {
                    generatedPassword: password,
                    copyToClipboard: () => {
                        this.clipboard.copyText(password);
                    }
                },
                controller: "ResourceController",
                templateUrl: 'src/app/portal/users/user-details/generated-password.html',
                preventClose: true,
                title: "New password",
                promptOnCancel: true
            };

            if (created) {
                options.dataModel.title = "EINVOICING.PORTAL.USERS.EDITOR.USER_CREATED_TITLE";
                options.dataModel.createdUser = true;
                options.dataModel.message = this.translator.
                    instantTranslate("EINVOICING.PORTAL.USERS.EDITOR.USER_CREATED_MESSAGE", password);
            } else {
                options.dataModel.title = "EINVOICING.PORTAL.USERS.EDITOR.PASSWORD_GENERATED_TITLE";
                options.dataModel.message = this.translator.
                    instantTranslate("EINVOICING.PORTAL.USERS.EDITOR.PASSWORD_GENERATED", password);
            }

            this.modalWindow.show(options);
        }

        /**
         * Saves form changes
         */
        save() {
            if (!this.validate()) {
                return;
            }

            var selectedRoles = _.filter(this.allRoles,
                (role) => {
                    return role.id === this.selectedRoleId;
                });

            var selectedWorkingFor = [];

            _.each(this.allWorkingFor,
                (model) => {
                    if (model.isAssigned === true) {
                        selectedWorkingFor.push(model.party);
                    }
                });

            var selectedAllocatedFor = [];

            _.each(this.allAllocatedTo,
                (model) => {
                    if (model.isAssigned === true && !this.isSupervisor) {
                        selectedAllocatedFor.push(model.party);
                    }
                });

            this.user.roles = selectedRoles;
            this.user.workingParties = selectedWorkingFor;
            this.user.allocatedParties = selectedAllocatedFor;
            this.user.editRestrictions = this.selectedRestriction.name.toLowerCase();
            this.user.rights = this.allRights;

            this.userProxy.saveUser(this.user).then((data) => {
                this.notificationHandler.showSuccess('EINVOICING.PORTAL.USERS.EDITOR.SAVED_SUCCESS');
                this.user.id = data;
                this.isDirty = false;
                this.loadUserData();
                this.removeAction(this.clientEnums.userActions.save);
            },
                (error) => {
                    this.isDirty = true;
                    this.notificationHandler.showError(error.failureReason);
                });
        }


        /**
         * calls dependency to send reset password email to edited user
         */
        resetPassword() {
            this.userSecurityProxy.requestPasswordResetOnUserBehalf(this.user.id, this.user.name).then((data) => {
                if (data.success) {
                    this.notificationHandler.
                        showSuccess('EINVOICING.PORTAL.USERS.EDITOR.RESET_SUCCESS', this.user.name);
                } else {

                    const message = `CORE.USER_SECURITY.ERRORS.${data.failureReason.toUpperCase()}`;

                    this.notificationHandler.showError(message);
                }
            },
                (error) => {
                    this.notificationHandler.showError(error.failureReason);
                });
        }

        /**
         * Confirms the user wishes to cancel their changes and then cancels the changes if required
         */
        cancelChanges() {
            this.loadUserData();
            this.isDirty = false;
        }

        delete() {
            this.userProxy.remove(this.user.id)
                .then(result => {
                    this.notificationHandler.showSuccess('EINVOICING.PORTAL.USERS.DELETE.SUCCESS', this.user.name);
                    this.backToList();
                },
                error => {
                    this.notificationHandler.showError(error.failureReason);
                });
        }
        /**
         * navigates back to user list
         */
        backToList() {
            this.diState.go('portal.users.list');
        }

        /**
         * Returns translated label for true or false state
         */
        getTrueFalseLabel(value) {
            if (util.isDefined(value) && value === true) {
                return this.translator.translate("EINVOICING.PORTAL.BODY.TRUE");
            } else {
                return this.translator.translate("EINVOICING.PORTAL.BODY.FALSE");
            }
        }

        /**
         * adds action to allowed actions        
         */
        addAction(actionId) {
            var found = _.find(this.user.actions,
                function (id) {
                    return id === actionId;
                });

            if (util.isUndefined(found)) {
                this.user.actions.push(actionId);
                this.resolveActions();
            }
        }

        /**
         * removes action from allowed actions
         */
        removeAction(actionId) {
            const flitered = _.filter(this.user.actions,
                function (id) {
                    return id !== actionId;
                });

            this.user.actions.fill(flitered);

            this.resolveActions();
        }

        /**
        *  resolves allowed actions
        */
        resolveActions() {
            const resolvedActions = this.userActionsResolver.resolveActions(this.user.actions, false);
            this.selectedActions.fill(resolvedActions);
        }

        /**
         * Adds spinner to the page
         */
        showSpinner() {
            this.spinner = this.modalSpinner.showModalSpinner("#spinnerHolder");
        }

        /**
         * Removes spinner from the page
        */
        removeSpinner() {
            if (this.spinner) {
                this.spinner.remove();
            }
        }
    }
}


UserDetailsComponent.controller.prototype.constructor.$inject = [
    '$stateParams',
    'userProxy',
    'partyProxy',
    'diState',
    'notificationHandler',
    'translator',
    'enumTranslator',
    'userDetailsConfigurationService',
    'authorizedObjectsFilter',
    'authenticationData',
    'CLIENT_ENUMS',
    'associativeArrayConvertor',
    'userRightsRepository',
    'userSecurityProxy',
    'userActionsResolver',
    'modalWindow',
    'modalSpinner',
    'clipboard',
    'validationRulesSetter'
];


