define([
	'./_snippets/_loader',
	'./invoiceDetails/invoiceDetailsDirective'
], function (
	snippetsLoader,
	invoiceDetailsDirectiveLoader) {
	'use strict';

	return function (module) {
		snippetsLoader(module);
		invoiceDetailsDirectiveLoader(module);
	};
});


