// di.core.angular.url
define([
	'./endpoints',
	'./urlHelper',
	'./absoluteUrl'
], function (
    endpointsLoader,
	urlHelperLoader,
	absoluteUrlLoader) {
	'use strict';

	return function (module) {
	    endpointsLoader(module);
		urlHelperLoader(module);
		absoluteUrlLoader(module);
	};
});



