//di.core.ng.controls
define([
], function () {
	'use strict';

	return function (module) {
		module.directive('diInput', diInput);

		diInput.$inject = ['$interpolate', 'translator', '$q', 'controlsRestrictionManager', 'formAnalyser'];

		function diInput($interpolate, translator, $q, controlsRestrictionManager, formAnalyser) {
			return {
				restrict: 'E',
				scope: {
					bindModel: '=ngModel',
					bindModelProperty: '@ngModelProperty',
					bindModelOptions: '=ngModelOptions',
				    validationPropertyName :"@",
					onChange: '&ngChange'
				},
				controller: 'InputController',
				controllerAs: 'inputCtrl',
				template: template,
				link: function (scope, element, attrs) {
					scope.inputClass = interpolate('inputClass');
					scope.diInputType = interpolate('diInputType');

					interpolateAndTranslate('diInputLabel').then(function (translated) {
				        scope.diInputLabel = translated;
					});

					scope.diShowIcon = interpolate('diShowIcon', 'true') === 'true';

					scope.validationModelDisplayName = interpolate('validationModelDisplayName');
					scope.validationMessagePosition = interpolate('validationMessagePosition');
					scope.validationMessage = interpolate('validationMessage');
					scope.validateOnBlur = interpolate('validateOnBlur', 'false') === 'true';
				    scope.diClass = interpolate('diClass');

					interpolateAndTranslate('placeholder').then(function (translated) {
					    scope.placeholder = translated;
					});

				    let modelName = getModelName();

				    if (scope.validationPropertyName && modelName) {
				        controlsRestrictionManager.addControlChangeSubscription(modelName + '.' + scope.validationPropertyName,
                            (disabled) => {
                                scope.disabled = disabled;
                            });
					}

				    return;

                    function getModelName() {
                        let elementWithValidatationModelName = formAnalyser.getElementWithValidationModel(element);
                        let innerElement = elementWithValidatationModelName[0];
                        if (innerElement) {
                            return elementWithValidatationModelName[0].attributes['di-validate-model'].value;
                        }
                        return '';
                    }

				    function interpolateAndTranslate(attr, valueIfEmpty) {
				        var value = interpolate(attr, valueIfEmpty);

				        if (value) {
				            return translator.translate(value);
				        }

				        return $q.when(value);
				    }

				    function interpolate(attr, valueIfEmpty) {
				        var value = attrs[attr];
				        var result = (value ? $interpolate(value)(scope) : '') || valueIfEmpty;

				        return result;
				    }
				}
			};

			/**
			* This should be placed in a seperate .html file - we need to provide deployment solution for this?
			* Perhaps npm - gulp-angular-templatecache?
			*/
			function template() {
				var html =
`<div>
	<div
		ng-if="::diInputType === \'date\'">
		<label
			ng-if="::diInputLabel"
			class="form-row-label">
			{{::diInputLabel}}
		</label>
		<div
			class ="datepicker"
            ng-if="::diShowIcon === true">
			<div
				class="datepicker-input">
				<span>
					<di-image
						class="datepicker-button"
						root-element="button"
						image="calendar"
						image-colour="button"
                        tabindex="-1"
						type="button">
					</di-image>
				</span>
				<input
					class="input form-control date-input"
					type="text"
					ng-model="bindModel[bindModelProperty]"
					ng-change="onChange()"
                    ng-disabled="::disabled"
					ng-click="inputCtrl.showDatePicker()"
					di-validate="{{::validationPropertyName}}"
					validate-on-blur="{{::validateOnBlur}}"
					validation-model-display-name="{{::validationModelDisplayName}}"
					validation-message-position="{{::validationMessagePosition}}"
					validation-message="{{::validationMessage}}"
					placeholder="{{::placeholder}}" />
			</div>
		</div>
        <div
			class ="datepicker"
            ng-if="::diShowIcon !== true">
			<div>				
				<input
					class ="input form-control date-input"
					type="text"
					ng-model="bindModel[bindModelProperty]"
					ng-change="onChange()"
                    ng-disabled="::disabled"
					ng-click="inputCtrl.showDatePicker()"
					di-validate="{{::validationPropertyName}}"
					validate-on-blur="{{::validateOnBlur}}"
					validation-model-display-name="{{::validationModelDisplayName}}"
					validation-message-position="{{::validationMessagePosition}}"
					validation-message="{{::validationMessage}}"
					placeholder="{{::placeholder}}" />
			</div>
		</div>
	</div>
	<div
		ng-if="::diInputType === \'number\'">
		<label
			ng-if="::diInputLabel"
			class="form-row-label">
			{{::diInputLabel}}
		</label>
		<input
			class="input form-control"
			type="number"
			pattern="/^\\d+$/"
			onkeypress="return event.charCode >= 48 && event.charCode <= 57"
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
			ng-change="onChange()"
            ng-disabled="::disabled"
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			validation-model-display-name="{{::validationModelDisplayName}}"
			validation-message-position="{{::validationMessagePosition}}"
			validation-message="{{::validationMessage}}"
			placeholder="{{::placeholder}}" />
	</div>
	<div
		ng-if="::diInputType === \'checkbox\'">
		<label
			class="checkbox">
			<input
				class="form-control checkbox-trigger"
				type="checkbox"
				ng-model="bindModel"
				ng-change="onChange()" />
			<div
				class="checkbox-base">
			</div>
			<span
				class="checkbox-label">{{::diInputLabel}}</span>
		</label>
	</div>
	<div
		ng-if="::diInputType === \'email\'">
		<label
			ng-if="::diInputLabel"
			class="form-row-label">
			{{::diInputLabel}}
		</label>
		<input
			class="input form-control"
			type="email"
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
			ng-change="onChange()"
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			validation-model-display-name="{{::validationModelDisplayName}}"
			validation-message-position="{{::validationMessagePosition}}"
			validation-message="{{::validationMessage}}"
			placeholder="{{::placeholder}}" />
	</div>
	<div
		ng-if="::diInputType === \'password\'">
		<label
			ng-if="::diInputLabel"
			class="form-row-label">
			{{::diInputLabel}}
		</label>
		<input
			class="input form-control"
			type="password"
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
			ng-change="onChange()"            
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			validation-model-display-name="{{::validationModelDisplayName}}"
			validation-message-position="{{::validationMessagePosition}}"
			validation-message="{{::validationMessage}}"
			placeholder="{{::placeholder}}" />
	</div>
	<div
		ng-if="::diInputType === \'decimal\'">
		<label
			ng-if="::diInputLabel"
			class="form-row-label">
			{{::diInputLabel}}
		</label>
		<input
			class="input form-control numericcell"
			type="text"
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
            ng-disabled="::disabled"
			ng-change="onChange()"
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			validation-model-display-name="{{::validationModelDisplayName}}"
			validation-message-position="{{::validationMessagePosition}}"
			validation-message="{{::validationMessage}}"
			placeholder="{{::placeholder}}" />
	</div>
	<div
		ng-if="::diInputType === \'text\'">
        <label
			ng-if="::diInputLabel"
			class="form-row-label">
			{{::diInputLabel}}
		</label>
		<input
			class="input form-control"
			type="text"
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
			ng-change="onChange()"
            ng-disabled="::disabled"
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			validation-model-display-name="{{::validationModelDisplayName}}"
			validation-message-position="{{::validationMessagePosition}}"
			validation-message="{{::validationMessage}}"
			placeholder="{{::placeholder}}" />
	</div>
    <div
		ng-if="::diInputType === \'hidden\'">
       
		<input
			class ="input form-control"
			type="hidden"
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
			ng-change="onChange()"
            ng-disabled="::disabled"
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			placeholder="{{::placeholder}}" />
	</div>
    <div
		ng-if="::diInputType === \'textarea\'">
        <label
			ng-if="::diInputLabel"
			class ="form-row-label">
				{{::diInputLabel}}
		</label>
		<textarea
			ng-class ="::diClass"			
			ng-model="bindModel[bindModelProperty]"
			ng-model-options="bindModelOptions"
			ng-change="onChange()"
            ng-disabled="::disabled"
			di-validate="{{::validationPropertyName}}"
			validate-on-blur="{{::validateOnBlur}}"
			validation-model-display-name="{{::validationModelDisplayName}}"
			validation-message-position="{{::validationMessagePosition}}"
			validation-message="{{::validationMessage}}"			
			placeholder="{{::placeholder}}">
        </textarea>
	</div>
</div>`;

				return html;
			}
		}

		return diInput;
	};
});



