define([
    './audit-list-configurator.provider',
	'./audit-list.controller',
	'./audit-list.directive'
], function (
	auditListConfiguratorLoader,
	auditListControllerLoader,
	auditListDirectiveLoader) {
	'use strict';

	return function (module) {
	    auditListConfiguratorLoader(module);
	    auditListControllerLoader(module);
	    auditListDirectiveLoader(module);
	};
});



