// di.core.angular.utility
define([
	'./associativeArrayConvertor',
	'./guardedInjector',
	'./uniqueIdGenerator',
	'./filters/_loader',
    './identityIncrementor',
    './randomStringGenerator'
], function (
    associativeArrayConvertorLoader,
	guardedInjectorLoader,
	uniqueIdGeneratorLoader,
	filtersLoader,
	identityIncrementorLoader,
    randomStringGeneratorLoader) {
	'use strict';

	return function (module) {
		associativeArrayConvertorLoader(module);
		guardedInjectorLoader(module);
		uniqueIdGeneratorLoader(module);
		filtersLoader(module);
		identityIncrementorLoader(module);
		randomStringGeneratorLoader(module);
	};
});



