// di.core.angular.window
define([
	'./diClickAwayDirective',
	'./windowEventSubscriber'
], function (
    diClickAwayDirectiveLoader,
    windowEventSubscriberLoader) {
	'use strict';

	return function (module) {
	    diClickAwayDirectiveLoader(module);
	    windowEventSubscriberLoader(module);
	};
});



