define([
    './dropdown-selector.directive',
    './dropdown-selector.controller'
], function (
    dropdownSelectorDirectiveLoader,
    dropdownSelectorControllerLoader) {
    'use strict';

    return function (module) {
        dropdownSelectorDirectiveLoader(module);
        dropdownSelectorControllerLoader(module);
    };
});


