define([
	'./supplierSummaryDirective',
	'./supplierSummaryController'
], function (
	supplierSummaryDirectiveLoader,
	supplierSummaryControllerLoader
	) {
	'use strict';

	return function (module) {
		supplierSummaryDirectiveLoader(module);
		supplierSummaryControllerLoader(module);
	};
});



