define([
    './_snippets/_loader',
    './invoiceList/invoiceListDirective'
], function (
    snippetsLoader,
    invoiceListDirectiveLoader) {
	'use strict';

	return function (module) {
		snippetsLoader(module);
		invoiceListDirectiveLoader(module);
	};
});


