let util = $di.utility.util;

export const DiscoverPartiesListComponent = {
    templateUrl: 'src/app/portal/parties/discovery/discover-parties-list.component.html',
    selector: 'diDiscoverPartiesList',
    bindings: {
        partyId: "<",
        onTradingPartyIdChanged: "&"
    },
    controllerAs: 'discoverPartiesListCtrl',
    controller:
    class DiscoverPartiesListController {

        constructor(
            partyProxy,
            discoverPartiesListConfigurationService,
            $q) {
            this.partyProxy = partyProxy;
            this.discoverPartiesListConfigurationService = discoverPartiesListConfigurationService;
            this.$q = $q;

            this.search = this.search.bind(this);
            this.selectedItemChanged = this.selectedItemChanged.bind(this);
            this.resetSelection = this.resetSelection.bind(this);
        }

        $onInit() {
            let tableOptionsPromise = this.discoverPartiesListConfigurationService.getTableOptions()
                .then(tableOptions => {
                    this.tableOptions = tableOptions;
                });
            this.resetSelection();
        }

        search(withDelay) {
            if (withDelay) {
                if (!this.delayStarted) {
                    this.delayStarted = true;
                    _.debounce(this.searchNoDelay, 1000).bind(this)();
                }
            } else {
                this.searchNoDelay();
            }
        }

        searchNoDelay() {
            this.loading = true;
            if (util.isNullOrUndefined(this.searchText) || this.searchText === '') {
                this.parties = [];
                this.allParties = [];
                this.resetSelection();
                this.loading = false;
                this.delayStarted = false;
            } else {
                this.partyProxy
                    .discoverPotentialTradingRelationshipParties(this.partyId, this.searchText)
                    .then(data => {
                        this.allParties = data;
                        this.parties = _.filter(this.allParties, (party) => {
                            return party.addressBookEntry.party.isAccountingCustomer == false;
                        });
                        this.resetSelection();
                    })
                    .finally(() => {
                        this.loading = false;
                        this.delayStarted = false;
                    });
            }
        }

        selectedItemChanged(items) {
            if (util.isNonEmptyArray(items) && items.length === 1) {

                let selectedItem = items[0];

                let partyId = selectedItem.addressBookEntry.party.isAccountingCustomer
                    ? selectedItem.addressBookEntry.party.buyers[0].id
                    : selectedItem.addressBookEntry.party.id;

                this.party = selectedItem.addressBookEntry.party.id === partyId
                    ? selectedItem
                    : _.find(this.parties, (party) => {
                        return party.addressBookEntry.party.id === partyId;
                    });

                var accountingCustomerForThisBuyer = _.find(this.allParties, (party) => {
                    if(party.addressBookEntry.party.isAccountingCustomer){
                        var selectedBuyer = _.find(party.addressBookEntry.party.buyers, (buyer) => {
                            return buyer.id === this.party.addressBookEntry.party.id;
                        });

                        if (util.isDefined(selectedBuyer)) {
                            return party;
                        }
                    }
                });

                this.accountingParties = [accountingCustomerForThisBuyer];

                this.onTradingPartyIdChanged({ tradingPartyId: this.party.addressBookEntry.party.id });
            }
            else {
                this.resetSelection();
            }
        }

        resetSelection() {
            this.party = undefined;
            this.accountingParties = undefined;

            this.onTradingPartyIdChanged({ tradingPartyId: undefined });
        }
    }
}

DiscoverPartiesListComponent.controller.prototype.constructor.$inject = ['partyProxy', 'discoverPartiesListConfigurationService', '$q'];


