define([
    './action-buttons.controller',
    './action-buttons.directive',
    './stateful-button/_loader'
], function(
    actionButtonsControllerLoader,
    diActionButtonsDirectiveLoader,
    statefulButtonLoader) {
    'use strict';

    return function (module) {
        statefulButtonLoader(module);
        actionButtonsControllerLoader(module);
        diActionButtonsDirectiveLoader(module);
    };
});


