// di.core.angular.watchers
define([
    './watcherHelper',
    './watchersTogglerDirective',
    './debounce'
], function(
    watcherHelperLoader,
    watchersTogglerDirectiveLoader,
    debounceLoader) {
    'use strict';

    return function (module) {
        watcherHelperLoader(module);
        watchersTogglerDirectiveLoader(module);
        debounceLoader(module);
    };
});



