export const NotificationSettingsComponent = {
    selector: 'diNotificationSettings',
    controller: class {
        constructor(
            accountService,
            associativeArrayConvertor, 
            clientEnums, 
            enumTranslator, 
            notificationHandler, 
            userProxy
            ) {
            this.accountService = accountService;
            this.associativeArrayConvertor = associativeArrayConvertor;
            this.clientEnums = clientEnums;
            this.enumTranslator = enumTranslator;
            this.notificationHandler = notificationHandler;
            this.userProxy = userProxy;
            
            this.setNotifications = this.setNotifications.bind(this);
            this.saveNotifications = this.saveNotifications.bind(this);
            this.setFrequencies = this.setFrequencies.bind(this);
            this.allNotificationFrequencies = [];
        }

        $onInit() {
            this.accountService.getUser(() => {
                this.setFrequencies();
                this.setNotifications();
            }, () => {});
        }

        setFrequencies() {
            let allFrequencies = this.associativeArrayConvertor.convert(this.clientEnums.notificationFrequency);

            $.each(allFrequencies, (key, value) => {
                if ($di.utility.util.isNumber(value))
                    this.allNotificationFrequencies.push({
                        id: value,
                        name: ''
                    });
            });

            this.enumTranslator.translateCollection(this.clientEnums.notificationFrequency, this.allNotificationFrequencies, "id", "name");
        }

        setNotifications() {
            this.notifiableEvents = this.accountService.notifiableEvents;
            _.each(this.notifiableEvents,
                (item) => {
                    this.enumTranslator.translate(this.clientEnums.eventType, item.notifiableEvent.eventType)
                        .then(function(translated) {
                            item.notifiableEvent.eventTypeName = translated;
                        });
                });

            _.each(this.notifiableEvents, (notification) => {
                notification.notifiableEvent.frequencies = [];
                notification.notifiableEvent.frequencies.fill(this.allNotificationFrequencies);

                _.each(notification.notifiableEvent.frequencies, (frq) => {
                    frq.enabled = false;
                    if (notification.notifiableEvent.notificationFrequency === frq.id) {
                        frq.enabled = true;
                        return;
                    }
                });
            });
        }

        saveNotifications() {
            let promise = this.userProxy.saveUserNotifications(
            {
                user: this.accountService.currentUser,
                notifiableEvents: this.notifiableEvents
            });

            promise.then(data => {
                this.setNotifications(data);

                this.notificationHandler.showSuccess('EINVOICING.PORTAL.ACCOUNT.MANAGEMENT.NOTIFICATIONS_SAVED');
            }, () => {
                this.notificationHandler.showError('EINVOICING.PORTAL.ACCOUNT.MANAGEMENT.NOTIFICATIONS_ERROR');
            });
        }
    },
    templateUrl: 'src/app/portal/account/notifications-settings/notifications-settings.component.html'
}
NotificationSettingsComponent.controller.prototype.constructor.$inject = [
    'accountService',
    'associativeArrayConvertor', 
    'CLIENT_ENUMS', 
    'enumTranslator',
    'notificationHandler',
    'userProxy'
];


