//{CODE:EUP1C}
// di.einvoicing.ui.portal.components
define(function () {
    'use strict';

    return function (module) {
        var guard = $di.exception.guard;

        module.factory('headerData', headerData);

        function headerData() {
            var service = {
                data: {
                    title: ''
                },
                set: set
            };

            return service;

            function set(header) {
                guard.throwIfNotAnObject("EUP1C01E", header, "header");

                service.data.title = header.title;
            }
        }

        return headerData;
    };
});


