define([
    './contact-details.directive',
    './contact-details.controller'
], function (
    contactDetailsDirectiveLoader,
    contactDetailsControllerLoader) {
    'use strict';

    return function (module) {
        contactDetailsDirectiveLoader(module);
        contactDetailsControllerLoader(module);
    };
});


