// di.core.angular.date
define([
	'./diDateFilter',
	'./diDateFormats',
	'./diMoment'
], function (
	diDateFilter,
	diDateFormats,
	diMoment
	) {
	'use strict';

	return function (module) {
		diDateFilter(module);
		diDateFormats(module);
		diMoment(module);
	};
});


