class RestrictionsStore {
    constructor(userEditRestrictionsProxy, sessionStore) {
        this._userEditRestrictionsProxy = userEditRestrictionsProxy;
        this._sessionStore = sessionStore;
    }

    loadRestrictions(invoiceId) {
        return this._userEditRestrictionsProxy.getEditRestrictions(invoiceId).then(result => {
            this._sessionStore.set('editRestrictions', result, 'restrictionsStore');
        });
    }
    
    getEditRestrictions() {
        return this._sessionStore.get('editRestrictions', 'restrictionsStore');
    }
}

RestrictionsStore.prototype.constructor.$inject = ['userEditRestrictionsProxy', 'sessionStore'];

export { RestrictionsStore }


