import * as ng from "angular";
import { Injectable, Inject } from 'ng-metadata/core';

export interface ResourceListTableOptions {
    enableSelect: boolean;
    multiSelect: boolean;
    enablePaging: boolean;
    fixedHeight: boolean;
    maxItemsToShow: number;
    paginationPageSize: number;
    paginationPageNumber: number;
    maxButtonsShown: number;
    columns: Array<ResourceListColumns>;
    actionColumns: Array<ResourceActionColumns>;
}

export interface ResourceListSortOptions {
    value: number;
    key: string;
    label: string
}

export interface ResourceListColumns {
    field: string;
    nameLanguageKey: string;
    template: string
}

export interface ResourceActionColumns {
    field: string;
    template: string;
    cellClass: string;
}

@Injectable()
export abstract class ResourceListConfigurationServiceBase {
    private _tableOptions: ResourceListTableOptions;

    public tableOptions(): ResourceListTableOptions {
        return ng.copy<ResourceListTableOptions>(this._tableOptions);
    }

    constructor() {
        this._tableOptions = {
			enableSelect: true,
			multiSelect: false,
			enablePaging: true,
			fixedHeight: true,
			maxItemsToShow: 500,
			paginationPageSize: 5,
			paginationPageNumber: 1,
            maxButtonsShown: 3,
            columns: [],
            actionColumns: []
		}
	}

    abstract getTableOptions(): Promise<ResourceListTableOptions>;
    abstract getSortOptions(): Promise<Array<ResourceListSortOptions>>;
}


