define([
	'./_states',
	'./shared/_loader',
	'./contacts/_loader',
	'./locations/_loader',
	'./list/_loader',
	'./discovery/_loader',
	'./party-codes/_loader',
    './loader'
],
	function (
        statesLoader,
		sharedLoader,
        contactsLoader,
        locationsLoader,
		listLoader,
		discoveryLoader,
		partyCodesLoader,
        componentsLoader) {
		'use strict';

		return function (module) {
			statesLoader(module);
			sharedLoader(module);
			contactsLoader.load(module);
		    locationsLoader.load(module);
			listLoader(module);
			discoveryLoader(module);
			partyCodesLoader(module);
		    componentsLoader.load(module);
		};
	});


