define([
	'./file-uploader.factory',
	'./document-downloader.factory'
], function (
	fileUploaderLoader, documentDownloaderLoader) {
	'use strict';

	return function (module) {
		fileUploaderLoader(module);
		documentDownloaderLoader(module);
	};
});


