import { Injectable, Inject } from 'ng-metadata/core';

import { ResourceHandlerServiceBase } from '../../shared/resource-handler-base.service';
import { PartyCodeConfigurationService } from './party-code-configuration.service';

@Injectable()
export class PartyCodeHandlerService extends ResourceHandlerServiceBase {
    public type: string = "partyCode";

    constructor(
        @Inject('partyProxy') private partyProxy,
        @Inject('partyCodeConfigurationService') private partyCodeConfigurationService: PartyCodeConfigurationService
    ) {
        super(partyCodeConfigurationService);
    }

    getResourceBook(partyId, forPartyId, sortBy) {
        return this.partyProxy.getPartyCodesForParty(partyId);
    }

    updateResource(resouce, resourceId) {
        return this.partyProxy.updatePartyCode(resouce);
    }

    addNewResource(resource, forPartyId) {
        let partyId = resource.partyId;

        return this.partyProxy.addNewPartyCode(partyId, resource);
    }

    removeResource(resource, resourceId) {
    }
}

PartyCodeHandlerService.prototype.constructor.$inject = ['partyProxy', 'partyCodeConfigurationService'];


