define([
	'./party-feature-enabled/_loader',
	'./session-preferences/_loader'
], function (
	partyFeatureEnabledLoader,
	sessionPreferencesLoader) {
    'use strict';

    return function (module) {
        partyFeatureEnabledLoader(module);
        sessionPreferencesLoader(module);
    };
});


