define([
    './user-edit-restrictions-proxy.service',
    './controls-restriction-manager.service',
    './restrictions-store.service'
], function (
    userEditRestrictionsProxy,
    controlsRestrictionManager,
    restrictionStore) {
    'use strict';

    return function (module) {
        module.service(
            'userEditRestrictionsProxy',
            userEditRestrictionsProxy.UserEditRestrictionsProxyService);

        module.service(
            'controlsRestrictionManager',
            controlsRestrictionManager.ControlsRestrictionManager);

        module.service(
            'restrictionStore',
            restrictionStore.RestrictionsStore);
    };
});



