define([
	'./_states',
	'./lists/_loader',
    './related-documents/_loader'
], function(
	statesLoader,
	listsLoader,
    relatedDocumentsLoader) {
	'use strict';

	return function(module) {
		statesLoader(module);
		listsLoader(module);
	    relatedDocumentsLoader(module);
	};
});


