define([
    './party-list.component.js',
    './party-list-table-configuration.service.js',
    './party-selector.component.js',
    './allocated-to-party-list.component.js',
    './working-for-party-list.component.js'
], function(
    partyListComponentLoader,
    partyListTableConfigurationServiceLoader,
    partySelectorComponentLoader,
    allocatedToPartyListComponentLoader,
    workingForPartyListComponentLoader) {
    'use strict';

    return function(module) {
        module.component(
            partyListComponentLoader.PartyListComponent.selector,
            partyListComponentLoader.PartyListComponent);        
        module.service(
            'PartyListTableConfigurationService',
            partyListTableConfigurationServiceLoader.PartyListTableConfigurationService);
        module.component(
            partySelectorComponentLoader.PartySelectorComponent.selector,
            partySelectorComponentLoader.PartySelectorComponent);
        module.component(
            allocatedToPartyListComponentLoader.AllocatedToPartyListComponent.selector,
            allocatedToPartyListComponentLoader.AllocatedToPartyListComponent);
        module.component(
            workingForPartyListComponentLoader.WorkingForPartyListComponent.selector,
            workingForPartyListComponentLoader.WorkingForPartyListComponent);
    };
});


