define([
	'./service-status-monitor.controller',
	'./service-status-monitor.directive',
	'./service-status-monitor.service',
    './server-proxy.factory'
], function (
	serviceStatusMonitorControllerLoader,
	serviceStatusMonitorDirectiveLoader,
	serviceStatusMonitorLoader,
    serverProxyLoader) {
	'use strict';

	return function(module) {
	    serverProxyLoader(module);
	    serviceStatusMonitorControllerLoader(module);
		serviceStatusMonitorDirectiveLoader(module);
		serviceStatusMonitorLoader(module);
	};
});


