export const LocationSelectorComponent = {
  templateUrl: 'src/app/portal/invoices/editor/location-selector/location-selector.component.html',
  selector: 'diLocationSelector',
  bindings: {
    partyId: '<',
    locationType: '@',
    preDeterminedLocation: '<',
    onLocationSelected: '&'
  },
  controller:
  class {
    constructor(
      partyProxy,
      clientEnums
    ) {
      this.partyProxy = partyProxy;
      this.clientEnums = clientEnums;

      this.selectedPartyLocation = {};
      this.partyLocations = [];
      this.onLocationChanged = this.onLocationChanged.bind(this);
    }

    $onInit() {
      this.getPartyLocations();
    }

    $onChanges() {
      this.getPartyLocations();
    }

    getPartyLocations() {
      if (this.partyId === 0 || this.partyId === null || 'undefined' === typeof this.partyId) {
        return;
      }

      this.partyProxy
        .getLocationsForParty(this.partyId, this.locationType)
        .then((data) => {
          let locationsData = data;

          if ("undefined" !== typeof locationsData) {
            this.partyLocations = locationsData;
          }
          else {
            return;
          }

          if (this.preDeterminedLocation !== null
            && 'undefined' !== typeof this.preDeterminedLocation) {


            let preDeterminedFound = getPredeterminedLocationFromList(
              this.partyLocations, 
              this.preDeterminedLocation, 
              this.selectedPartyLocation);

            this.selectedPartyLocation = setSelectedLocation(
              this.partyLocations, 
              this.selectedPartyLocation,
              preDeterminedFound, 
              this.preDeterminedLocation);
          }
        });

      function getPredeterminedLocationFromList(partyLocations, preDeterminedLocation, selectedPartyLocation) {
        _.each(partyLocations, function (partyLocation) {
          if (partyLocation.addressLine1 === preDeterminedLocation.addressLine1
            && partyLocation.addressLine2 === preDeterminedLocation.addressLine2
            && partyLocation.town === preDeterminedLocation.addressTown
            && partyLocation.county === preDeterminedLocation.county
            && partyLocation.postalCode === preDeterminedLocation.postCode) {

            return partyLocation;
          }
        });
      }

      function setSelectedLocation(partyLocations, selectedPartyLocation, preDeterminedFound, preDeterminedLocation) {
        if ('undefined' !== typeof preDeterminedFound) {
          partyLocations.push(preDeterminedLocation);
          return preDeterminedLocation;
        } else {
          return preDeterminedLocation;
        }
      }

    }

    onLocationChanged(selectedLocation) {
      this.onLocationSelected({ value: selectedLocation });
    }
  }
}

LocationSelectorComponent.controller.prototype.constructor.$inject = ['partyProxy', 'CLIENT_ENUMS'];


