import { Injectable, Inject } from 'ng-metadata/core';

import {
    ResourceListConfigurationServiceBase,
    ResourceListTableOptions,
    ResourceListSortOptions
} from './resource-list-configuration-base.service';

@Injectable()
export abstract class ResourceHandlerServiceBase {
    constructor(private resourceListConfigurationService: ResourceListConfigurationServiceBase) {
    }

    init(partyId: number, forPartyId: number, sortBy: number = 1) {
        const tableOptionsPromise: Promise<ResourceListTableOptions> = this.resourceListConfigurationService.getTableOptions();
        const sortOptionsPromise: Promise<Array<ResourceListSortOptions>> = this.resourceListConfigurationService.getSortOptions();
        const resourcesPromise = this.getResourceBook(partyId, forPartyId, sortBy);

        return [resourcesPromise, tableOptionsPromise, sortOptionsPromise];
    }

    abstract getResourceBook(partyId: number, forPartyId: number, sortyBy: number);
    abstract addNewResource(resource, forPartyId);
    abstract updateResource(resource, resourceId);
    abstract removeResource(resource, resourceId);
}


