define([
	'./client-enums',
	'./enum-translator.service',
	'./enums-key-resolver.service'
], function (
    clientEnumsLoader,
	enumTranslatorLoader,
	enumsKeyResolverLoader) {
    'use strict';

    return function (module) {
        clientEnumsLoader(module);
        enumTranslatorLoader(module);
        enumsKeyResolverLoader(module);
    };
});


