define([
	'./_states',
	'./grn-repository.factory',
    './grn-proxy.factory',
    './grn-action-buttons/_loader',
    './grn-details/_loader',
    './grn-footer/_loader',
    './grn-header/_loader',
    './grn-lines/_loader',
    './grn-list/_loader'
], function(
	statesLoader,
	grnRepositoryLoader,
    grnProxyLoader,
    grnActionButtonsLoader,
    grnDetailsLoader,
    grnFooterLoader,
    grnHeaderLoader,
    grnLinesLoader,
    grnListLoader) {
	'use strict';

	return function(module) {
	    grnProxyLoader(module);
	    statesLoader(module);
		grnRepositoryLoader(module);
		grnActionButtonsLoader(module);
		grnDetailsLoader(module);
		grnFooterLoader(module);
		grnHeaderLoader(module);
		grnLinesLoader(module);
	    grnListLoader(module);
	};
});


