define([
    './action-buttons/_loader',
    './address-view-details/_loader',
    './contact-view-details/_loader',
    './date-selector/_loader',
    './additional-document-references/_loader',
    './document-state-selector/_loader',
    './document-state-display/_loader',
    './legal-view-details/_loader',
    './party-view-details/_loader',
    './address-edit-details/_loader',
    './contact-details/_loader',
    './set-focus/_loader'
], function(
	actionButtonsLoader,
    addressViewDetailsLoader,
    contactViewDetailsLoader,
    dateSelectorLoader,
    additionalDocumentReferencesLoader,
    documentStateSelectorLoader,
    documentStateDisplayLoader,
    legalViewDetailsLoader,
    partyViewDetailsLoader,
    addressEditDetailsLoader,
    contactDetailsLoader,
    setFocusLoader) {
	'use strict';

	return function(module) {
	    actionButtonsLoader(module);
	    addressViewDetailsLoader(module);
	    contactViewDetailsLoader(module);
	    dateSelectorLoader(module);
	    additionalDocumentReferencesLoader(module);
	    documentStateSelectorLoader(module);
	    documentStateDisplayLoader(module);
	    legalViewDetailsLoader(module);
	    partyViewDetailsLoader(module);
	    addressEditDetailsLoader(module);
	    contactDetailsLoader(module);
        setFocusLoader(module);
    };
});



