define([
    './contact-selector.directive',
    './contact-selector.controller'
], function (
    contactSelectorDirectiveLoader,
    contactSelectorControllerLoader) {
    'use strict';

    return function (module) {
        contactSelectorDirectiveLoader(module);
        contactSelectorControllerLoader(module);
    };
});


