define([
	'./invoiceLinesColumnSettings',
    './scrollbarResizer',
    './invoiceActionVerifier'
], function (
	invoiceLinesColumnSettingsLoader,
    scrollbarResizerLoader,
    invoiceActionVerifierLoader) {

	'use strict';

	return function (module) {
		invoiceLinesColumnSettingsLoader(module);
		scrollbarResizerLoader(module);
		invoiceActionVerifierLoader(module); 
	};
});



