define([
    './_snippets/_loader',
    './header-data.service',
    './header.controller'
], function (
    snippetsLoader,
    headerDataLoader,
    headerControllerLoader) {
    'use strict';

    return function (module) {
        headerControllerLoader(module);
        headerDataLoader(module);
        snippetsLoader(module);
    };
});


