//di.einvoicing.ui.portal.portal.users
define([
	'./user-list.component',
	 './user-list-configuration.service'
], function (
	userListComponentModule,
	userListConfigurationServiceLoader
	) {
    'use strict';

    return function (module) {
        module.component(
			userListComponentModule.UserListComponent.selector,
			userListComponentModule.UserListComponent);
        module.service('userListConfigurationService', userListConfigurationServiceLoader.UserListConfigurationService);
    };
});


