import { Component, Inject, Injectable } from 'ng-metadata/core';
import { ModalControllerBase } from '../../../../lib/di.core/spa/controls/modal/modal-base.controller';

@Injectable()
export class AddPartyComponentController extends ModalControllerBase {
    public partyId;
    public saveButtonText;
    public title;

    constructor(
        @Inject('$element') $element,
        @Inject('promptWindow') promptWindow,
        @Inject('options') options,
        @Inject('close') close
    ) {
        super($element, promptWindow, options, close);

        this.partyId = options.partyId;
        this.saveButtonText = options.saveButtonText;
        this.title = options.modalTitle;
    }

    tradingPartyIdChanged(tradingPartyId) {
        this.model = tradingPartyId;
    }
}

AddPartyComponentController.prototype.constructor.$inject = ['$element', 'promptWindow', 'options', 'close']



