// di.core.angular.controls.select
define([
	'./diObjectFilter',
	'./diSelectController',
	'./diSelectDirective',
    './diSelectUpgrade',
    './diSelectUpgradeController'
], function (
	objectFilterLoader,
	selectControllerLoader,
	selectDirectiveLoader,
    selectDirectiveUpgradeLoader,
    selectUpgradeControllerLoader) {
	'use strict';

	return function (module) {
	    objectFilterLoader(module);
	    selectControllerLoader(module);
	    selectDirectiveLoader(module);
	    selectUpgradeControllerLoader(module);
	    selectDirectiveUpgradeLoader(module);
	};
});



