// di.core.angular.validation
define([
	'./diValidateClickDirective',
	'./diValidateDirective',
	'./diValidateModelDirective',
	'./diRequireIfDirective',
	'./requireIfValidator',
	'./formAnalyser',
	'./idValidator',
	'./positiveWholeNumberValidator',
	'./ruleViolationSelector',
	'./validationMessageGenerator',
	'./validationNotifier',
	'./validationInitialiser',
	'./validationRulesProxy',
	'./validationRulesStore',
    './validationRulesSetter',
    './pastOrTodayValidator',
    './comparers/_loader',
    './greaterValidator',
    './lessValidator',
    './equalAlwaysValidator',
    './equalIfSelfSetValidator',
    './requiredIfPairSetValidator',
    './requiredIfPairNotSetValidator',
    './product-code-custom-validator.service'
], function (
	diValidateClickDirectiveLoader,
	diValidateDirectiveLoader,
	diValidateDirectiveModelLoader,
	diRequireIfDirectiveLoader,
	requireIfValidatorLoader,
	formAnalyserLoader,
	idValidatorLoader,
	positiveWholeNumberValidatorLoader,
	ruleViolationSelectorLoader,
	validationMessageGeneratorLoader,
	validationNotifierLoader,
	validationInitialiserLoader,
	validationRulesProxyLoader,
	validationRulesStoreLoader,
    validationRulesSetterLoader,
    pastOrTodayValidatorLoader,
    comparersLoader,    
	greaterValidatorLoader,
	lessValidatorLoader,
	equalAlwaysValidatorLoader,
	equalIfSelfSetValidatorLoader,
	requiredIfPairSetValidatorLoader,
	requiredIfPairNotSetValidatorLoader,
    productCodeCustomValidator
	) {
	'use strict';

	return function (module) {
		diValidateClickDirectiveLoader(module);
		diValidateDirectiveLoader(module);
		diValidateDirectiveModelLoader(module);
		diRequireIfDirectiveLoader(module);
		requireIfValidatorLoader(module);
		formAnalyserLoader(module);
		idValidatorLoader(module);
		positiveWholeNumberValidatorLoader(module);
		ruleViolationSelectorLoader(module);
		validationMessageGeneratorLoader(module);
		validationNotifierLoader(module);
		validationInitialiserLoader(module);
		validationRulesProxyLoader(module);
		validationRulesStoreLoader(module);
	    validationRulesSetterLoader(module);
		pastOrTodayValidatorLoader(module);
		comparersLoader(module);
		greaterValidatorLoader(module);
		lessValidatorLoader(module);
		equalAlwaysValidatorLoader(module);
		equalIfSelfSetValidatorLoader(module);
		requiredIfPairSetValidatorLoader(module);
		requiredIfPairNotSetValidatorLoader(module);
		module.service('productCodeCustomValidator', productCodeCustomValidator.ProductCodeCustomValidator);
	};
});



