// di.einvoicing.ui.portal.portal.invoices.lists
define(['./_components/_loader',
	'./invoiceListController',
	'./invoiceListConfigurator'
], function (
	componentsLoader,
	invoiceListControllerLoader,
	invoiceListConfiguratorLoader) {
	'use strict';

	return function (module) {
		componentsLoader(module);
		invoiceListConfiguratorLoader(module);
		invoiceListControllerLoader(module);		
	};
});



