import { Component, Input, Output, EventEmitter } from 'ng-metadata/core';

@Component({
    selector: 'diDocumentStateSelector',
    templateUrl: 'src/app/portal/shared/components/document-state-selector/document-state-selector.html'
})
export class DocumentStateSelectorComponent{
    @Input() items:Array<any>;
    @Input() image;
    @Input() set selectedState(a) {
        this.item = a;
        console.log(this.item);
    }
   
    @Output() onChangedState = new EventEmitter<any>();

    public item: any;
    constructor() {

    
    }

    public selectState(item) {
        if (!item) 
            return;
        
        if (this.item === item)
            return;

        this.item = item;
       

        if (this.onChangedState) {
            this.onChangedState.emit(item.value);
        }
    }
}


