define([
    './stateful-button.controller',
    './stateful-button.directive'
], function(
    statefulButtonControllerLoader,
    statefulButtonDirectiveLoader) {
    'use strict';

    return function(module) {
    	statefulButtonControllerLoader(module);
        statefulButtonDirectiveLoader(module);
    };
});


