// di.core.angular.state
define([
	'./stateHelper',
	'./diState',
	'./stateAuthenticator',
	'./stateDecorator',
], function (
	stateHelperLoader,
	diStateLoader,
    stateAuthenticatorLoader,
    stateDecoratorLoader
	) {
	'use strict';

	return function (module) {
		stateHelperLoader(module);
		diStateLoader(module);
	    stateAuthenticatorLoader(module);
	    stateDecoratorLoader(module);
	};
});



