define([
    './date-selector.directive',
    './date-selector.controller'
], function (
    diDateSelectorDirectiveLoader,
    diDateSelectorControllerLoader) {
    'use strict';

    return function (module) {
        diDateSelectorDirectiveLoader(module);
        diDateSelectorControllerLoader(module);
    };
});


